import React, { FC } from 'react';
import NavCard from './components/NavCard';
import { useAppSelector } from '../../../useHook/useRedux';
import './navMenu.css';

const NavMenu: FC = () => {
    const data = useAppSelector((state) => state.menu.category);

    return (
        <>
            <nav className='navMenu'>
                {data &&
                    data.map(({ id, name, img_path }) => (
                        <NavCard key={name} id={id} name={name} img_path={img_path} />
                    ))}
            </nav>
        </>
    );
};

export default NavMenu;
