import React, { FC } from 'react';
import HeaderClient from './header/HeaderClient';
import Menu from './menu/Menu';
import { useAppSelector } from '../useHook/useRedux';
import BasketIcon from './basket/BasketIcon';
import useWindowDimensions from '../utils/useWindowDimensions';
import './main.css';
import AsideBasketDekstop from './asideBasketDekstop/AsideBasketDekstop';
const MainDefault: FC = () => {
    const { widthCreen } = useWindowDimensions();
    const titleMenu = useAppSelector((state) => state.menu.activeCategory?.name);
    return (
        <>
            <HeaderClient />

            {widthCreen && widthCreen < 500 && <span className='navigate_span'>{titleMenu}</span>}
            {widthCreen && widthCreen < 900 ? (
                <Menu />
            ) : (
                <div className='mainMenuClientDekstop_wrapper'>
                    <div className='mainMenuClientDekstop_menuWrapper'>
                        <span className='navigate_span'>{titleMenu}</span>
                        <Menu />
                    </div>
                    <AsideBasketDekstop />
                </div>
            )}
            {widthCreen && widthCreen < 900 && <BasketIcon />}
        </>
    );
};

export default MainDefault;
