import { FC, useState, Fragment } from 'react';
import { NavLink } from 'react-router-dom';
import { Twirl as HamburgerIcon } from 'hamburger-react';
import icon_home from '../../../style/image/icon_home.png';
import icon_order from '../../../style/image/icon_order.png';
import icon_user from '../../../style/image/icon_user.png';
import icon_menu from '../../../style/image/icon_menu.png';
import icon_nav from '../../../style/image/Icon_nav_burger.svg';

import './asideBarNav.css';
import { useLanguage } from '../../../utils/LanguageContext';

interface MenuItem {
    label: string;
    to: string;
    icon?: any;
    subMenu?: MenuItem[];
}

const menuItems: MenuItem[] = [
    {
        label: 'Dashboard',
        icon: <img src={icon_home} />,
        to: '/',
    },
    {
        label: 'Меню',
        icon: <img src={icon_menu} />,
        to: '/menu',
        subMenu: [
            {
                label: 'category',
                to: '/menu/category',
            },
            {
                label: 'stock',
                to: '/menu/stock',
            },
            {
                label: 'product',
                to: '/menu/product',
            },
            {
                label: 'addition',
                to: '/menu/addition',
            },
        ],
    },
    {
        label: 'Order',
        icon: <img src={icon_order} />,
        to: '/order',
    },
    {
        label: 'User',
        to: '/user',
        icon: <img src={icon_user} />,
        subMenu: [
            {
                label: 'admin',
                to: '/user/admin',
            },
            {
                label: 'worker',
                to: '/user/worker',
            },
            {
                label: 'client',
                to: '/user/client',
            },
        ],
    },
];

const AsideBarNav: FC = () => {
    const [open, setOpen] = useState(true);
    const toggleOpen = () => {
        setOpen(!open);
    };
    const { languageSlug } = useLanguage();
    const renderMenuItem = (menuItem: MenuItem) => {
        if (menuItem.subMenu) {
            return (
                <Fragment key={menuItem.to}>
                    <NavLink
                        key={menuItem.to}
                        className='sideitem'
                        onClick={() => setOpen(true)}
                        to={`/${languageSlug}${menuItem.to}`}>
                        {menuItem.icon}
                        <span className='linkText'>{menuItem.label}</span>
                        <img className='navbar_burger' src={icon_nav} />
                    </NavLink>
                    <div className='nav_aside_submenu' style={open ? undefined : { display: 'none' }}>
                        {menuItem.subMenu.map((subItem) => (
                            <NavLink key={subItem.label} to={`/${languageSlug}${subItem.to}`}>
                                {subItem.label}
                            </NavLink>
                        ))}
                    </div>
                </Fragment>
            );
        }

        return (
            <NavLink key={menuItem.to} className='sideitem' to={`/${languageSlug}${menuItem.to}`}>
                {menuItem.icon}
                <span className='linkText'>{menuItem.label}</span>
            </NavLink>
        );
    };
    return (
        <div className={open ? 'sidenav' : 'sidenavClosed'}>
            <button className='menuBtn' onClick={toggleOpen}>
                <HamburgerIcon size={30} toggled={open} toggle={setOpen} />
            </button>
            <nav className='navbar'>{menuItems.map((menuItem) => renderMenuItem(menuItem))}</nav>
        </div>
    );
    // return <nav className='navbar'>{menuItems.map((menuItem) => renderMenuItem(menuItem))}</nav>;
};

export default AsideBarNav;
