import { FC } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { setActiveCategory } from '../../../../../store/slices/menuSlice';
interface NavCardProps {
    id: number;
    name: string;
}
const WorkerMobileNavCard: FC<NavCardProps> = ({ id, name }) => {
    const dispatch = useAppDispatch();
    const activeCategory = useAppSelector((state) => state.menu.activeCategory);

    const activeToggle = () => {
        dispatch(setActiveCategory({ id, name }));
    };

    const cardStyle = activeCategory.id === id ? { backgroundColor: '#0E9594', color: '#fff' } : {};

    return (
        <div data-id={id} onClick={activeToggle} style={cardStyle} className='navMenu_worker_mobile'>
            <span>{name}</span>
        </div>
    );
};

export default WorkerMobileNavCard;
