import { FC, useState } from 'react';
import icon_drop from '../../../../style/image/drop_icon.png';

interface WorkerBasketDropProps {
    amount: string[];
    activeAmount: string;
    setAmount: (arg: string) => void;
}
const WorkerBasketDrop: FC<WorkerBasketDropProps> = ({ amount, activeAmount, setAmount }) => {
    const [dropOn, setDropOn] = useState(false);
    const filteredAmount = amount.filter((i) => i !== activeAmount);
    return (
        <div
            className={`worker_basket_drop_wrapper ${dropOn && filteredAmount.length > 0 ? 'active' : ''}`}
            onClick={() => setDropOn(() => !dropOn)}>
            <div className='worker_basket_drop' onClick={() => setAmount(activeAmount)}>
                <div className='worker_basket_drop_title'>{activeAmount}</div>
                {filteredAmount.length > 0 && <img src={icon_drop} alt='' className={dropOn ? 'rotate__img' : ''} />}
            </div>

            {dropOn && (
                <div className='worker_basket_drop_select_wrapper'>
                    {filteredAmount.map((am) => (
                        <span key={am} onClick={() => setAmount(am)} className='worker_basket_drop_select'>
                            {am}
                        </span>
                    ))}
                </div>
            )}
        </div>
    );
};

export default WorkerBasketDrop;
