import { FC, useState, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import UseModal from '../../../../../useHook/useModal/UseModal';
import { getStock, addStockToProduct, Stock } from '../../../../../store/slices/menuSlice';
import { stockColumn } from '../columnsMenu';
import ButtonAdmin from '../../ButtonAdmin';
interface ProductAddStockProps {
    productStock?: Stock[];
}
const ProductAddStock: FC<ProductAddStockProps> = ({ productStock }) => {
    const [modal, setModal] = useState(false);
    const dispatch = useAppDispatch();
    const stock = useAppSelector((state) => state.menu.stock);

    const productStockIds = productStock?.map((item) => item.id);
    const filteredStock = stock.filter((item) => !productStockIds?.includes(item.id));

    useEffect(() => {
        dispatch(getStock());
    }, []);
    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };

    return (
        <>
            <ButtonAdmin text='Add Stock' handle={onModal} />
            <UseModal
                visible={modal}
                title={`Add Stock`}
                content={
                    <>
                        <UseTable
                            columns={stockColumn}
                            data={filteredStock}
                            link={false}
                            rel={true}
                            addRel={true}
                            addFunc={addStockToProduct}
                        />
                    </>
                }
                onClose={closeModal}
            />
        </>
    );
};

export default ProductAddStock;
