import React, { FC } from 'react';
import { useAppSelector } from '../../../../../useHook/useRedux';
import { useParams } from 'react-router-dom';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import { stockColumn, additionColumn, categoryColumn, productColumn } from '../columnsMenu';
import {
    removeStockFromProduct,
    removeAdditionFromProduct,
    removeCategoryFromProduct,
} from '../../../../../store/slices/menuSlice';
import ProductAddStock from './ProductAddStock';
import ProductAddAddition from './ProductAddAddition';
import ProductAddCategory from './ProductAddCategory';
import ProductPut from './ProductPut';

const ProductDetails: FC = () => {
    const { id } = useParams();
    const product = useAppSelector((state) => state.menu.product).filter(
        (product) => product.id == parseInt(id ? id : ''),
    )[0];

    return (
        <div>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Product</span>
                <ProductPut
                    id={product.id}
                    name={product.name}
                    amount={product.amount}
                    description={product.description}
                    price={product.price}
                    Stocks={product.Stocks}
                    Additions={product.Additions}
                />
            </div>

            <div className='adminDetails_item_wrapper'>
                {productColumn.map((i) => {
                    if (!product[i.accessor]) return;
                    return (
                        <div className='adminDetails_item'>
                            <span>{i.header}: </span>
                            <span> {product[i.accessor]}</span>
                        </div>
                    );
                })}
            </div>

            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Stocks:</span>
                <ProductAddStock productStock={product?.Stocks} />
            </div>

            {product?.Stocks && (
                <UseTable
                    columns={stockColumn}
                    data={product.Stocks}
                    link={false}
                    delFunc={removeStockFromProduct}
                    rel={true}
                />
            )}

            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Additions:</span>
                <ProductAddAddition productAddition={product?.Additions} />
            </div>

            {product?.Additions && (
                <UseTable
                    columns={additionColumn}
                    data={product.Additions}
                    link={false}
                    delFunc={removeAdditionFromProduct}
                    rel={true}
                />
            )}

            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Categories:</span>
                <ProductAddCategory productCategory={product?.Categories} />
            </div>

            {product?.Categories && (
                <UseTable
                    columns={categoryColumn}
                    data={product?.Categories}
                    link={false}
                    delFunc={removeCategoryFromProduct}
                    rel={true}
                />
            )}
        </div>
    );
};

export default ProductDetails;
