import { FC } from 'react';
import WorkerMobileNavCard from './WorkerMobileNavCard';
import { useAppSelector } from '../../../../../useHook/useRedux';
import './workerMobileMenu.css';
const WorkerMobileNav: FC = () => {
    const category = useAppSelector((state) => state.menu.category);

    return (
        <>
            <nav className='navMenu_worker'>
                {category && category.map(({ id, name }) => <WorkerMobileNavCard key={name} id={id} name={name} />)}
            </nav>
        </>
    );
};

export default WorkerMobileNav;
