import { FC, useEffect, useState } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../useHook/useRedux';
import { getOrder } from '../../../../store/slices/orderSlice';
import UseTable from '../../../../useHook/UseTable/UseTable';
import { formatDateTimeString } from '../../../../utils/changeData';
import { orderColumn } from './columnsOrder';

const OrderMain: FC = () => {
    const dispatch = useAppDispatch();
    const orderData = useAppSelector((state) => state.order.order).map(({ updatedAt, createdAt, ...rest }) => ({
        updatedAt: updatedAt === createdAt ? '' : formatDateTimeString(updatedAt),
        createdAt: formatDateTimeString(createdAt),
        ...rest,
    }));

    useEffect(() => {
        dispatch(getOrder());
    }, []);

    return (
        <>
            {orderData && (
                <UseTable
                    link={true}
                    columns={orderColumn}
                    data={orderData}
                    // delFunc={delStock}
                />
            )}
        </>
    );
};

export default OrderMain;
