import React, { FC } from 'react';

import './useModal.css';

interface UseModalProps {
    visible: boolean;
    title?: string;
    content: React.ReactNode;
    footer?: React.ReactNode;
    onClose: () => void;
    classStyle?: string;
}

const UseModal: FC<UseModalProps> = ({ visible, title, content, footer, onClose, classStyle }) => {
    return (
        <>
            {visible && (
                <div
                    className={` ${classStyle} modal`}
                    onClick={(e) => {
                        e.stopPropagation();
                        onClose();
                    }}>
                    <div className='modal_dialog' onClick={(e) => e.stopPropagation()}>
                        {title && (
                            <div className='modal_header'>
                                <h3 className='modal_title'>{title}</h3>
                            </div>
                        )}
                        <div className='modal_body'>
                            <div className='modal_content'>{content}</div>
                        </div>
                        {footer && <div className='modal_footer'>{footer}</div>}
                    </div>
                </div>
            )}
        </>
    );
};

export default UseModal;
