import { FC, useEffect } from 'react';
import './modalForProduct.css';
import WorkerMobileModalContetnProduct from './ModalDekstopContent';
import { Product } from '../../../store/slices/menuSlice';
import { Basket } from '../../../store/slices/orderSlice';
interface ModalDekstopProps {
    isOpen: boolean;
    closeModal: () => void;
    product: Product[];
    order?: Basket;
    lang: string;
}
const ModalDekstop: FC<ModalDekstopProps> = ({ isOpen, closeModal, product, order, lang }) => {
    useEffect(() => {
        if (isOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'auto';
        }
        return () => {
            document.body.style.overflow = 'auto';
        };
    }, [isOpen]);
    return (
        <>
            {isOpen && (
                <div className='modalClientDekstop' onClick={closeModal}>
                    <div className='modalClientDekstop_content' onClick={(e) => e.stopPropagation()}>
                        <WorkerMobileModalContetnProduct
                            lang={lang}
                            closeModal={closeModal}
                            product={product}
                            order={order}
                        />
                    </div>
                </div>
            )}
        </>
    );
};

export default ModalDekstop;
