import { FC, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { getAdmin } from '../../../../../store/slices/userAdminSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import UserAdminCreate from './UserAdminCreate';
import { delUser } from '../../../../../store/slices/userAdminSlice';
import { userColumn } from '../columnUsers';
import '../userAdminMain.css';
const UserAdmin: FC = () => {
    const dispatch = useAppDispatch();
    const adminData = useAppSelector((state) => state.adminUser.admin);
    useEffect(() => {
        dispatch(getAdmin());
    }, []);

    return (
        <>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Admin</span>
                <UserAdminCreate />
            </div>
            <span className='userMain_title'></span>
            {adminData && <UseTable columns={userColumn} link={true} data={adminData} delFunc={delUser} />}
        </>
    );
};

export default UserAdmin;
