import React, { FC } from 'react';
import { Route, Routes } from 'react-router-dom';
import { defaultRoutes, workerRoutes, adminRoutes } from './routes';
import { useAppSelector } from '../useHook/useRedux';

const AppRouter: FC = () => {
    const typeUser = useAppSelector((state) => state.user?.data?.type);

    switch (typeUser) {
        case 'worker':
            return (
                <Routes>
                    {workerRoutes.map(({ path, element }) => (
                        <Route key={path} path={path} element={element} />
                    ))}
                </Routes>
            );
        case 'admin':
            return (
                <Routes>
                    {adminRoutes.map(({ path, element }) => (
                        <Route key={path} path={path} element={element} />
                    ))}
                </Routes>
            );

        default:
            return (
                <Routes>
                    {defaultRoutes.map(({ path, element }) => (
                        <Route key={path} path={path} element={element} />
                    ))}
                </Routes>
            );
    }
};

export default AppRouter;
