import { FC, useEffect, useState } from 'react';
import { useAppDispatch } from '../../../useHook/useRedux';
import UseModal from '../../../useHook/useModal/UseModal';
import details_icon from '../../../style/image/details_icon.png';
import approved_icon from '../../../style/image/approved_icon.png';
import loading_icon from '../../../style/image/loading_icon.png';
import cancel_icon from '../../../style/image/cancel_icon.png';
import { putOrderWorker } from '../../../store/slices/orderSlice';
import useWindowDimensions from '../../../utils/useWindowDimensions';
import { API_URL } from '../../../axios';
interface ModalOrderStatusProps {
    order_id: number;
    order_status: string;
}
const ModalOrderStatus: FC<ModalOrderStatusProps> = ({ order_id, order_status }) => {
    const [modal, setModal] = useState(false);
    const { widthCreen } = useWindowDimensions();
    const dispatch = useAppDispatch();

    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };
    const changeOrderStatus = (handle: string) => {
        dispatch(putOrderWorker({ formData: { status: handle }, id: order_id }));
        closeModal();
    };

    return (
        <>
            <img
                src={details_icon}
                alt='details icon'
                onClick={(e) => {
                    e.stopPropagation();
                    onModal();
                }}
                className='orderWorkerDetailsIcon'
            />
            <UseModal
                classStyle={widthCreen && widthCreen < 500 ? 'workerOrder' : ''}
                content={
                    <div className='modalWorker_status'>
                        <span className='modalWorker_status_title'>Select order status</span>
                        <div className='modalWorker_status_wrapperButton'>
                            {order_status !== 'pending' && (
                                <div onClick={() => changeOrderStatus('done')}>
                                    <img src={approved_icon} alt='approved icon' />
                                    <span>Order approved</span>
                                </div>
                            )}

                            {order_status !== 'process' && (
                                <div onClick={() => changeOrderStatus('process')}>
                                    <img src={loading_icon} alt='loading_icon' />
                                    <span>Order in process</span>
                                </div>
                            )}

                            <div onClick={() => changeOrderStatus('cancel')}>
                                <img src={cancel_icon} alt='cancel icon' />
                                <span>
                                    Cancel
                                    <br />
                                    order
                                </span>
                            </div>
                        </div>
                    </div>
                }
                visible={modal}
                onClose={closeModal}
            />
        </>
    );
};

export default ModalOrderStatus;
