import { Formik, Field, Form, ErrorMessage, FieldProps } from 'formik';
import { FC } from 'react';

import * as Yup from 'yup';
import { useAppDispatch, useAppSelector } from '../../useHook/useRedux';
import InputMask from 'react-input-mask';
import { sendOrderUser, clearBasketAndOrderWorker } from '../../store/slices/orderSlice';
import { useLanguage } from '../../utils/LanguageContext';
import { BasketTr, PriceTr } from '../../constatns/translateDefaultUser';
const BasketClientForm: FC = () => {
    const dispatch = useAppDispatch();
    const { languageSlug } = useLanguage();
    const basket = useAppSelector((state) => state.order.basket);
    const totalPrice = basket.reduce(
        (total, item) => total + item.price + item.additions.reduce((total, item) => total + item.price, 0),
        0,
    );
    const send = (comment: string) => {
        const orderData = basket.map((item) => {
            return {
                count: item.count,
                product_id: item.product.id,
                addition_ids: item.additions.map(({ id }) => id),
            };
        });

        dispatch(sendOrderUser({ data: orderData, payment: 'unpaid', comment })).then((res) => {
            if (res.payload === 200) {
                dispatch(clearBasketAndOrderWorker());
            }
        });
    };

    return (
        <Formik
            initialValues={{
                user_name: '',
                phone: '',
                comment: '',
            }}
            validationSchema={Yup.object({
                user_name: Yup.string().required('Enter name!'),
                phone: Yup.string()
                    // .min(12, "min 12")
                    .required('required')
                    .test('no-underscore', 'Phone number cannot contain underscores', (value) => {
                        return !value || !value.includes('_');
                    }),
            })}
            onSubmit={async (body, actions) => {
                send(body.comment);
                actions.resetForm();
            }}>
            {({ setFieldValue, handleChange, handleBlur, errors, touched }) => (
                <div className='basketClientForm_wrapper'>
                    <span className='basketClientForm_title'>{BasketTr.form.placing[languageSlug]}</span>

                    <Form className=''>
                        <div>
                            <div className='basketClientForm_price_wrapper'>
                                <span>{BasketTr.form.orderAmount[languageSlug]}</span>
                                <span>
                                    {totalPrice} {PriceTr.currency[languageSlug]}
                                </span>
                            </div>
                            <div className='basketClientForm_person'>
                                <span>{BasketTr.form.personal[languageSlug]}</span>

                                <Field
                                    className=''
                                    type='text'
                                    placeholder={BasketTr.form.placInputForm.name[languageSlug]}
                                    name='user_name'
                                />
                                <ErrorMessage name='user_name' className='error' component='div' />

                                <Field name='phone'>
                                    {({ field }: FieldProps) => (
                                        <InputMask
                                            {...field}
                                            mask='+38(099) 999-9999'
                                            id='phone'
                                            placeholder={BasketTr.form.placInputForm.phone[languageSlug]}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            className='form__input'
                                        />
                                    )}
                                </Field>
                                <ErrorMessage name='phone' className='error' component='div' />
                            </div>

                            <div className='basketClientForm_comment'>
                                <span>{BasketTr.form.comment[languageSlug]}</span>
                                <Field
                                    as='textarea'
                                    placeholder={BasketTr.form.placInputForm.comment[languageSlug]}
                                    name='comment'
                                />
                                <ErrorMessage name='comment' className='error' component='div' />
                            </div>
                        </div>

                        <button className='basketClientForm_submit' type='submit'>
                            {BasketTr.form.confirm[languageSlug]}
                        </button>
                    </Form>
                </div>
            )}
        </Formik>
    );
};

export default BasketClientForm;
