import MainDefault from '../defaultUser/MainDefault';
import SignIn from '../defaultUser/SignIn/SignIn';
import Basket from '../defaultUser/basket/Basket';
import Search from '../defaultUser/search/Search';

import MainWorker from '../worker/MainWorker';
import MainAdmin from '../admin/MainAdmin';

interface Route {
    path: string;
    element: React.ReactNode;
}

export const defaultRoutes: Route[] = [
    {
        path: '/',
        element: <MainDefault />,
    },
    {
        path: '/signin',
        element: <SignIn />,
    },
    {
        path: '/basket',
        element: <Basket />,
    },
    {
        path: '/search',
        element: <Search />,
    },
];

export const workerRoutes: Route[] = [
    {
        path: '/*',
        element: <MainWorker />,
    },
];

export const adminRoutes: Route[] = [
    {
        path: '/*',
        element: <MainAdmin />,
    },
];
