import { FC, useEffect } from 'react';
import WorkerMobileProductCard from './WorkerMobileProductCard';
import { getMenu, setActiveCategory, Product } from '../../../../../store/slices/menuSlice';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { GroupedProducts } from '../../WorkerMenuProduct';

const WorkerMobileProducts: FC = () => {
    const dispatch = useAppDispatch();
    const { menu, activeCategory } = useAppSelector((state) => state.menu);
    const firstGetMenu = async () => {
        await dispatch(getMenu());
        menu[0]?.id && setActiveCategory(menu[0].id);
    };
    useEffect(() => {
        firstGetMenu();
    }, []);

    const data: Product[] = menu
        .filter(({ id }) => id === activeCategory.id)
        .flatMap(({ Products }) => Products) as Product[];

    const groupedProducts: GroupedProducts = data.reduce((groups: GroupedProducts, product: Product) => {
        const { name } = product;

        if (groups[name]) {
            groups[name].push(product);
        } else {
            groups[name] = [product];
        }

        return groups;
    }, {});
    return (
        <div className='menu'>
            {groupedProducts &&
                Object.values(groupedProducts).map((product, index) => (
                    <WorkerMobileProductCard key={index} product={product} />
                ))}
        </div>
    );
};

export default WorkerMobileProducts;
