import { FC } from 'react';
import { NavLink } from 'react-router-dom';
import { useAppDispatch } from '../../../../useHook/useRedux';
import { setBasketWorker } from '../../../../store/slices/orderSlice';
import { GroupedProducts } from '../WorkerMenuProduct';
import './workerMenuCard.css';
import { useLanguage } from '../../../../utils/LanguageContext';
interface WorkerMenuCardProps {
    img_src?: string;
    title: string;
    to?: string;
    products?: GroupedProducts;
}

const WorkerMenuCard: FC<WorkerMenuCardProps> = ({ img_src, title, to, products }) => {
    const dispatch = useAppDispatch();
    const { languageSlug } = useLanguage();
    const handleProduct = () => {
        if (products) {
            const changeProductsKey = products && { [Math.random()]: Object.values(products).flat() };
            dispatch(setBasketWorker(changeProductsKey));
        }
    };
    return (
        <>
            {to ? (
                <NavLink to={`/${languageSlug}${to}`} className='worker-menu_card'>
                    <div className='worker-menu_card_img-container'>{img_src && <img src={img_src} alt={title} />}</div>
                    <span className='worker-menu_card_text'>{title}</span>
                </NavLink>
            ) : (
                <div className='worker-menu_card' onClick={() => handleProduct()}>
                    <div className='worker-menu_card_img-container'>{img_src && <img src={img_src} alt={title} />}</div>
                    <span className='worker-menu_card_text'>{title}</span>
                </div>
            )}
        </>
    );
};

export default WorkerMenuCard;
