import { FC, useEffect, useState } from 'react';
import { useAppSelector, useAppDispatch } from '../../useHook/useRedux';
import { NavLink } from 'react-router-dom';
import { setUserOrderStore } from '../../store/slices/orderSlice';
import HeaderClient from '../header/HeaderClient';
import BasketCardMobile from './BasketCardMobile';
import BasketCardDekstop from './BasketClientDekstopCard';
import BasketClientForm from './BasketClientForm';
import Spinner from '../../utils/spiner/Spinner';
import useWindowDimensions from '../../utils/useWindowDimensions';
import back from '../../style/image/back_black.png';
import { API_URL } from '../../axios';
import { fetchEventSource } from '@microsoft/fetch-event-source';
import { useLanguage } from '../../utils/LanguageContext';
import { BasketTr } from '../../constatns/translateDefaultUser';
const Basket: FC = () => {
    const dispatch = useAppDispatch();
    const { widthCreen } = useWindowDimensions();
    const { languageSlug } = useLanguage();
    // const [additionCheck, setAdditionCheck] = useState<number[]>([]);
    const basket = useAppSelector((state) => state.order.basket);
    // const handleAddition = (additionId: number) => {
    //     if (additionCheck.includes(additionId)) {
    //         setAdditionCheck(additionCheck.filter((id) => id !== additionId));
    //     } else {
    //         setAdditionCheck([...additionCheck, additionId]);
    //     }
    // };
    const userOrder = useAppSelector((state) => state.order.userOrderStore);

    useEffect(() => {
        // const token = localStorage.getItem('token');
        // const headers = {
        //     Authorization: `Bearer ${token}`,
        // };
        const controller = new AbortController();
        if (userOrder?.status === 'pending') {
            const connectEventSource = async () => {
                await fetchEventSource(`${API_URL}/connect_order_user/${userOrder.id}`, {
                    // headers,
                    signal: controller.signal,
                    async onopen(response) {
                        // if (response.status === 401) {
                        //     const response = await axios.get<RefreshPayload>(`${API_URL}/refresh`, {
                        //         withCredentials: true,
                        //     });
                        //     localStorage.setItem('token', response.data.acsses);
                        // }
                    },
                    onmessage(event) {
                        const order = JSON.parse(event.data);
                        dispatch(setUserOrderStore(order));
                    },
                });
            };
            connectEventSource();
        }

        return () => {
            controller.abort();
        };
    }, [userOrder]);

    if (userOrder?.status === 'pending') {
        return <Spinner text={BasketTr.statusOrder.empConfirm[languageSlug]} />;
    }
    if (userOrder?.status === 'process') {
        return <Spinner text={BasketTr.statusOrder.payment[languageSlug]} />;
    }
    return (
        <>
            {widthCreen && widthCreen > 900 && <HeaderClient nav={false} />}
            <div className='basket_header'>
                <NavLink to='/'>
                    <img src={back} alt='' />
                </NavLink>

                <span>{BasketTr.myOrder[languageSlug]}</span>
            </div>
            <div className='basket_wrapper'>
                <div className='basket_content'>
                    {widthCreen && widthCreen > 900 ? (
                        <div className='basket_card_container_dekstop'>
                            {basket.map((basket) => (
                                <BasketCardDekstop
                                    key={basket.keyInOrder}
                                    keyInOrder={basket.keyInOrder}
                                    product={basket.product}
                                    additions={basket.additions}
                                    price={basket.price}
                                    count={basket.count}
                                />
                            ))}
                        </div>
                    ) : (
                        <div className='basket_card_container_mobile'>
                            {basket.map((basket) => (
                                <BasketCardMobile
                                    key={basket.keyInOrder}
                                    keyInOrder={basket.keyInOrder}
                                    product={basket.product}
                                    additions={basket.additions}
                                    price={basket.price}
                                    count={basket.count}
                                />
                            ))}
                        </div>
                    )}
                    <BasketClientForm />
                </div>
            </div>
            {/* {widthCreen && widthCreen < 900 && (
                <div className='basket_bottom_mobile'>
                    <button onClick={send}>Оформить заказ: {totalPrice} грн.</button>
                </div>
            )} */}
        </>
    );
};

export default Basket;

