import React, { FC, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../useHook/useRedux';
import { getMenu, Product, setActiveCategory } from '../../store/slices/menuSlice';
import MenuCard from './card/MenuCard';
import './menu.css';
export interface GroupedProducts {
    [name: string]: Product[];
}
const Menu: FC = () => {
    const dispatch = useAppDispatch();
    const { menu, activeCategory } = useAppSelector((state) => state.menu);

    useEffect(() => {
        dispatch(getMenu());
    }, []);

    const data: Product[] = menu
        .filter(({ id }) => id === activeCategory.id)
        .flatMap(({ Products }) => Products) as Product[];

    const groupedProducts: GroupedProducts = data.reduce((groups: GroupedProducts, product: Product) => {
        const { name } = product;

        if (groups[name]) {
            groups[name].push(product);
        } else {
            groups[name] = [product];
        }

        return groups;
    }, {});
    return (
        <div className='menu'>
            {groupedProducts &&
                Object.values(groupedProducts).map((product, index) => <MenuCard key={index} product={product} />)}
        </div>
    );
};

export default Menu;
