import React, { Fragment, useState } from 'react';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
import DeleteItemTable from './DeleteItemTable';
import AddRelToItem from './AddRelToItem';
import ModalOrderStatus from '../../../worker/components/orders/ModalOrderStatus';
import OrderModalForTable from '../../../worker/components/orders/OrderModalForTable';
const BodyTable = ({ data, columns, link, delFunc, rel, addRel, addFunc, order, product_id, styleBody }) => {
    const locale = useLocation().pathname;
    const [selectedItem, setSelectedItem] = useState(null);

    const onModal = (id) => {
        setSelectedItem(id);
    };

    const closeModal = () => {
        setSelectedItem(null);
    };

    return data?.map((item, index) => {
        return (
            <Fragment key={item.id + index}>
                <tr key={item.id} onClick={() => onModal(item.id)} className={styleBody}>
                    {typeof item === 'object' &&
                        columns.map(({ accessor }) => {
                            if (accessor === 'del' && addRel) return;
                            if (accessor === 'del') {
                                return (
                                    <td key={accessor}>
                                        <DeleteItemTable
                                            item={item}
                                            order={order}
                                            delFunc={delFunc}
                                            rel={rel}
                                            product_id={product_id}
                                        />
                                    </td>
                                );
                            }
                            if (accessor === 'totalPrice' && styleBody === 'worker') {
                                return (
                                    <td key={accessor} style={{ position: 'relative' }}>
                                        {item[accessor]}
                                        <ModalOrderStatus order_status={item.status} order_id={item.id} />
                                    </td>
                                );
                            }

                            return (
                                <td key={accessor}>
                                    {link ? <Link to={`${locale}/${item.id}`}>{item[accessor]}</Link> : item[accessor]}
                                </td>
                            );
                        })}
                    {addRel && (
                        <td key={index} style={{ width: '50px' }}>
                            <AddRelToItem item={item} addFunc={addFunc} rel={rel} />
                        </td>
                    )}
                </tr>
                {styleBody === 'worker' && selectedItem === item.id && (
                    <OrderModalForTable order={item} visible={true} onClose={closeModal} />
                )}
            </Fragment>
        );
    });
};

export default BodyTable;
