import { FC, useState } from 'react';
import { useAppSelector, useAppDispatch } from '../../../../useHook/useRedux';
import { useNavigate } from 'react-router-dom';
import WorkerBasketCard from './WorkerBasketCard';
import WorkerBasketAddition from './WorkerBasketAddition';
import { Addition, Product } from '../../../../store/slices/menuSlice';
import { clearBasketAndOrderWorker, setOrderSend, setOrderWorkerTotal } from '../../../../store/slices/orderSlice';
import '../workerMenu.css';
import { useLanguage } from '../../../../utils/LanguageContext';

const WorkerBasket: FC = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const { languageSlug } = useLanguage();
    const [order, setOrder] = useState<{ [key: string]: { product: Product; count: number; additions?: Addition[] } }>(
        {},
    );
    const [additionSelectedProduct, setAdditionSelectedProduct] = useState<{ switch: boolean; key_in_order: string }>({
        switch: false,
        key_in_order: '',
    });
    const basketData = useAppSelector((state) => state.order.basketWorker);

    //work with addition
    const additionsChosenProduct = additionSelectedProduct.switch
        ? order[additionSelectedProduct.key_in_order].product.Additions
        : [];
    const additionsСhosen = additionSelectedProduct.switch ? order[additionSelectedProduct.key_in_order].additions : [];
    const handlerAddition = (on: boolean, key: string) => {
        setAdditionSelectedProduct(() => ({ key_in_order: key, switch: on }));
    };

    const addAdditionToBasket = (key: string, addition: Addition[]) => {
        setOrder((prevOrder) => {
            const updatedOrder = { ...prevOrder };
            updatedOrder[key].additions = addition;
            return updatedOrder;
        });
    };

    // woork with order
    const handleOrder = (key: string, selectedProduct: { product: Product; count: number; additions?: Addition[] }) => {
        setOrder((prevOrder) => ({
            ...prevOrder,
            [key]: selectedProduct,
        }));
    };

    const delItemOrder = (key: string) => {
        setOrder((prevOrder) => {
            const updatedOrder = { ...prevOrder };
            delete updatedOrder[key];
            return updatedOrder;
        });
    };
    const sendOrder = () => {
        const orderData = Object.values(order).map((item) => {
            return {
                count: item.count,
                product_id: item.product.id,
                addition_ids: item.additions ? item.additions.map(({ id }) => id) : [],
            };
        });
        dispatch(setOrderSend(orderData));
        dispatch(setOrderWorkerTotal(price));
        navigate(`/${languageSlug}/payment`);
    };
    // count price
    const price = Object.values(order).reduce(
        (total, product) =>
            total +
            (product.product.price +
                (product.additions ? product.additions.reduce((total, addit) => total + addit.price, 0) : 0)) *
                product.count,
        0,
    );

    const handleCancel = () => {
        dispatch(clearBasketAndOrderWorker());
        setOrder({});
    };
    return (
        <>
            <div className='worker_basket'>
                <span className='worker_basket_header'>Order</span>
                <div className='worker_basket_T-head'>
                    <span>Name</span>
                    <span>Q-ty</span>
                    <span>Size</span>
                    <span>Price</span>
                </div>
                <div className='worker_basket_products'>
                    {basketData.map((products, index) => {
                        const [keyProducts, valueProducts] = Object.entries(products).flat();
                        const key = typeof keyProducts === 'string' ? keyProducts : '';
                        const value = typeof valueProducts !== 'string' ? valueProducts : [];
                        return (
                            <WorkerBasketCard
                                key={typeof keyProducts === 'string' ? keyProducts : ''}
                                indexProduct={index}
                                keyInBasket={key}
                                handleOrder={handleOrder}
                                delItemOrderMethod={delItemOrder}
                                handlerAddition={handlerAddition}
                                additionsСhosen={order[key]?.additions}
                                products={value}
                            />
                        );
                    })}
                </div>

                <div className='worker_basket_bottom'>
                    <div className='worker_basket_bottom_text'>
                        <span>Total price:</span>
                        <span>{price} грн.</span>
                    </div>
                    <div className='worker_basket_bottom_button'>
                        <button onClick={handleCancel}>Cancel</button>
                        <button onClick={sendOrder}>Approve</button>
                    </div>
                </div>
            </div>
            {additionSelectedProduct.switch && (
                <WorkerBasketAddition
                    handlerAdditionBasket={handlerAddition}
                    additionsСhosen={additionsСhosen}
                    additionsChosenProduct={additionsChosenProduct}
                    addAdditionToBasket={addAdditionToBasket}
                    key_product_in_basket={additionSelectedProduct.key_in_order}
                />
            )}
        </>
    );
};

export default WorkerBasket;
