import React from 'react';
import { signIn } from '../../store/slices/userSlice';
import { useAppSelector, useAppDispatch } from '../../useHook/useRedux';
import { useNavigate } from 'react-router-dom';
import { Formik, Field, Form, ErrorMessage } from 'formik';
import * as Yup from 'yup';
import './SignIn.css';

interface SignInFormValues {
    login: string;
    password: string;
}

const SignIn: React.FC = () => {
    const dispatch = useAppDispatch();
    const errorSignIn = useAppSelector((state: any) => state.user.error);
    const navigate = useNavigate();
    return (
        <Formik
            initialValues={{
                login: '',
                password: '',
            }}
            validationSchema={Yup.object({
                password: Yup.string().required('Enter password!'),
                login: Yup.string().required('Enter login!'),
            })}
            onSubmit={async (body: SignInFormValues) => {
                const req = await dispatch(signIn(body));
                if (req.payload === 200) navigate('/');
            }}
        >
            <div className='wrapper_signin'>
                <h1 className='head'>Sign In</h1>
                {errorSignIn ? <div>Wrong password or login</div> : ''}
                <ErrorMessage name='login' className='error user' component='div' />

                <Form className='signin'>
                    <Field className='user' type='text' placeholder='Enter login' name='login' />

                    <Field className='pass' type='password' placeholder='Enter password' name='password' />

                    <button type='submit'>&#xf0da;</button>
                </Form>
                <ErrorMessage name='password' className='error password' component='div' />
            </div>
        </Formik>
    );
};

export default SignIn;
