import { FC, useState, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import UseModal from '../../../../../useHook/useModal/UseModal';
import { getCategory, addCategoryToProduct, Category } from '../../../../../store/slices/menuSlice';
import { categoryColumn } from '../columnsMenu';
import ButtonAdmin from '../../ButtonAdmin';
interface ProductAddCategoryProps {
    productCategory?: Category[];
}
const ProductAddCategory: FC<ProductAddCategoryProps> = ({ productCategory }) => {
    const [modal, setModal] = useState(false);
    const dispatch = useAppDispatch();
    const category = useAppSelector((state) => state.menu.category);

    const productCategoryIds = productCategory?.map((item) => item.id);
    const filteredCategory = category.filter((item) => !productCategoryIds?.includes(item.id));

    useEffect(() => {
        dispatch(getCategory());
    }, []);
    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };

    return (
        <>
            <ButtonAdmin text='Add Category' handle={onModal} />
            <UseModal
                visible={modal}
                title={`Add Category`}
                content={
                    <>
                        <UseTable
                            columns={categoryColumn}
                            data={filteredCategory}
                            link={false}
                            rel={true}
                            addRel={true}
                            addFunc={addCategoryToProduct}
                        />
                    </>
                }
                onClose={closeModal}
            />
        </>
    );
};

export default ProductAddCategory;
