import { FC, useState, useEffect } from 'react';
import logo from '../../../style/image/logo.png';
import date_icon from '../../../style/image/date_icon.png';
import AdminHeaderUserIcon from './AdminHeaderUserIcon';
import { useAppSelector } from '../../../useHook/useRedux';
import useWindowDimensions from '../../../utils/useWindowDimensions';
import './adminHeader.css';
const AdminHeader: FC = () => {
    const [formattedDate, setFormattedDate] = useState<string>('');
    const [formattedTime, setFormattedTime] = useState<string>('');
    const userName = useAppSelector((state) => state.user.data?.name);
    const timeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
    const { widthCreen } = useWindowDimensions();
    useEffect(() => {
        const updateFormattedDateAndTime = () => {
            const currentDate = new Date();
            const optionsDate: Intl.DateTimeFormatOptions = {
                timeZone: timeZone,
                year: 'numeric',
                month: '2-digit',
                day: '2-digit',
            };

            const optionsTime: Intl.DateTimeFormatOptions = {
                timeZone: timeZone,
                hour: '2-digit',
                minute: '2-digit',
            };

            const dateFormatter = new Intl.DateTimeFormat(undefined, optionsDate);
            const timeFormatter = new Intl.DateTimeFormat(undefined, optionsTime);

            const formattedDate = dateFormatter.format(currentDate);
            const formattedTime = timeFormatter.format(currentDate);

            setFormattedDate(formattedDate);
            setFormattedTime(formattedTime);
        };

        updateFormattedDateAndTime(); // Обновляем дату и время сразу
        const intervalId = setInterval(updateFormattedDateAndTime, 60000); // Каждую минуту

        return () => {
            clearInterval(intervalId);
        };
    }, [timeZone]);

    return (
        <div className='admin_header_wrapper'>
            <header className='worker-header'>
                <div className='worker-header_container'>
                    <img src={logo} alt='logo' />
                </div>
                <div className='worker-header_date'>
                    {widthCreen && widthCreen > 500 && <img src={date_icon} alt='icon date' />}
                    <span className='worker-header_date_hour'>{formattedTime}</span>
                    <span className='worker-header_date_day'>{formattedDate}</span>
                </div>
                <div className='worker-header_options'>
                    <AdminHeaderUserIcon />

                    <span>{userName}</span>
                </div>
            </header>
        </div>
    );
};

export default AdminHeader;
