import { FC } from 'react';
import { delItemBasket } from '../../store/slices/orderSlice';
import { useAppDispatch } from '../../useHook/useRedux';
import { Basket } from '../../store/slices/orderSlice';
import { PriceTr } from '../../constatns/translateDefaultUser';

import icon_del_red from '../../style/image/icon_del_red.png';
import { useLanguage } from '../../utils/LanguageContext';
const AsideBasketDekstopCard: FC<Basket> = ({ product, count, keyInOrder }) => {
    const dispatch = useAppDispatch();
    const { languageSlug } = useLanguage();
    const handleCancel = () => dispatch(delItemBasket(keyInOrder));
    return (
        <div className='mainMenuClientDekstop_asideBasket_card'>
            <img className='mainMenuClientDekstop_asideBasket_card_prodImg' src={product.img_path} alt='product' />

            <div className='mainMenuClientDekstop_asideBasket_card_promo'>
                <div className='mainMenuClientDekstop_asideBasket_card_promo_top'>
                    <span className='mainMenuClientDekstop_asideBasket_card_promo_name'>{product.name}</span>
                    <img
                        className='mainMenuClientDekstop_asideBasket_card_promo_delete'
                        onClick={handleCancel}
                        src={icon_del_red}
                        alt='cancel icon'
                    />
                </div>
                <span className='mainMenuClientDekstop_asideBasket_card_promo_bottom'>
                    {product.price}
                    {count > 1 && `X${count}`}
                    {PriceTr.currency[languageSlug]}
                </span>
            </div>
        </div>
    );
};

export default AsideBasketDekstopCard;
