import { FC, useState } from 'react';
import ModalMobileForProductProps from '../modalForCard/ModalMobileForProduct';
import ModalDekstop from '../modalForCard/ModalDekstop';
import useWindowDimensions from '../../../utils/useWindowDimensions';
import { Product } from '../../../store/slices/menuSlice';
import './menuCard.css';
import { useLanguage } from '../../../utils/LanguageContext';
import { MenuTrDef } from '../../../constatns/translateDefaultUser';
interface MenuCardProps {
    product: Product[];
}
const MenuCard: FC<MenuCardProps> = ({ product }) => {
    const [isOpen, setIsOpen] = useState(false);
    const { languageSlug } = useLanguage();
    const { widthCreen } = useWindowDimensions();
    const openModal = () => {
        setIsOpen(true);
    };

    const closeModal = () => {
        setIsOpen(false);
    };
    return (
        <>
            <div className='menuCard'>
                <img className='menuCard_img' src={product[0].img_path} alt='Menu card' />

                <div className='menuCard_text'>
                    <div>
                        <span>{product[0].name}</span>
                        <span>{product[0].description}</span>
                    </div>
                    <button onClick={openModal} className='menuCard_button'>
                        {MenuTrDef.menuCardChoise[languageSlug]}
                    </button>
                </div>
            </div>

            {widthCreen && widthCreen > 500 ? (
                <ModalDekstop lang={languageSlug} isOpen={isOpen} closeModal={closeModal} product={product} />
            ) : (
                <ModalMobileForProductProps
                    lang={languageSlug}
                    isOpen={isOpen}
                    closeModal={closeModal}
                    product={product}
                />
            )}
        </>
    );
};

export default MenuCard;
