import { FC, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { getCategory, delCategory } from '../../../../../store/slices/menuSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import CategoryCreate from './CategoryCreate';
import { categoryColumn } from '../columnsMenu';
import './../adminMenu.css';
const CategoryMain: FC = () => {
    const dispatch = useAppDispatch();
    const additionData = useAppSelector((state) => state.menu.category);
    useEffect(() => {
        dispatch(getCategory());
    }, []);

    return (
        <>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Category</span>
                <CategoryCreate />
            </div>

            {additionData && (
                <UseTable columns={categoryColumn} data={additionData} link={true} delFunc={delCategory} />
            )}
        </>
    );
};

export default CategoryMain;
