import React from 'react';
import { NavLink } from 'react-router-dom';
import { useAppDispatch } from '../../useHook/useRedux';
import { setUserOrderStore } from '../../store/slices/orderSlice';
import { useLanguage } from '../LanguageContext';
import { BasketTr } from '../../constatns/translateDefaultUser';
import './spinner.css';
const Spinner = ({ text }) => {
    const dispatch = useAppDispatch();
    const { languageSlug } = useLanguage();
    return (
        <div className='spinner-container'>
            <div className='loading-spinner'></div>
            <span>{text}</span>

            <NavLink onClick={() => dispatch(setUserOrderStore(null))} to='/'>
                {BasketTr.statusOrder.cancel[languageSlug]}
            </NavLink>
        </div>
    );
};

export default Spinner;
