import React, { FC } from 'react';
import { useState } from 'react';
import HeaderTable from './components/HeaderTable';
import BodyTable from './components/BodyTable';
import Pagination from './pagination/Pagination';

import './index.css';

interface useTableProps<T> {
    columns: any[];
    data: any[];
    link?: boolean;
    delFunc?: any;
    addFunc?: any;
    rel?: boolean;
    addRel?: boolean;
    order?: string;
    product_id?: number;
    style?: string;
}
const UseTable: FC<useTableProps<number>> = React.memo(
    ({ columns, data, link, delFunc, rel, addRel, addFunc, order, product_id, style }) => {
        // pagination
        const [currentPage, setCurrentPage] = useState(1);
        const [PageSize, setPageSize] = useState(25);
        const totalPages = data.length;
        const startIndex = (currentPage - 1) * PageSize;
        const endIndex = Math.min(startIndex + PageSize, data.length);
        if (currentPage - 1 > totalPages / PageSize) {
            setCurrentPage(1);
        }

        // change data for table
        const dataAddCount = data?.slice(startIndex, endIndex).map((i, index) => ({
            num: startIndex + index + 1,
            ...i,
        }));

        // get data redux and filter data

        // //  state filters
        // const [dataInput, setDataInput] = useState({
        //   driver_name: "",
        //   trailer: "",
        //   driver_passport: "",
        //   driver_phone: "",
        //   driver_license: "",
        //   transport_id: "",
        //   transport_name: "",
        //   culture: "",
        //   port: "",
        //   transport_type: "",
        //   // sender: "",
        //   dateMin: "",
        //   dateMax: "",
        // });
        // input onChannge
        // const onChangeData = (name: string, data: []) => {
        //   setDataInput((dataInput) => ({
        //     ...dataInput,
        //     [name]: data,
        //   }));
        // };

        // request by filter data
        // useEffect(() => {
        //   dispatch(getFilterData({ body: makeUrl(dataInput), offset }));
        // }, [dataInput, currentPage]);

        return (
            <div className={`wrapper_table ${style}`}>
                {data?.length > 0 && (
                    <>
                        <table className={style}>
                            <thead>
                                <HeaderTable
                                    // dataInput={dataInput}
                                    // onChangeData={onChangeData}
                                    styleHead={style}
                                    columns={columns}
                                    addRel={addRel}
                                />
                            </thead>
                            <tbody>
                                <BodyTable
                                    data={dataAddCount}
                                    link={link}
                                    columns={columns}
                                    delFunc={delFunc}
                                    rel={rel}
                                    addRel={addRel}
                                    addFunc={addFunc}
                                    order={order}
                                    product_id={product_id}
                                    styleBody={style}
                                />
                            </tbody>
                        </table>
                        <Pagination
                            className='pagination-bar'
                            currentPage={currentPage}
                            totalCount={totalPages}
                            pageSize={PageSize}
                            onPageChange={(page: React.SetStateAction<number>) => setCurrentPage(page)}
                        />
                    </>
                )}
            </div>
        );
    },
);

export default UseTable;
