import React, { FC, useState, useEffect } from 'react';
import MenuCard from '../menu/card/MenuCard';
import { Product } from '../../store/slices/menuSlice';
import { GroupedProducts } from '../menu/Menu';
import { useAppSelector } from '../../useHook/useRedux';
import backImg from '../../style/image/back.png';
import { SearchTr } from '../../constatns/translateDefaultUser';
import { NavLink } from 'react-router-dom';
import './search.css';
import { useLanguage } from '../../utils/LanguageContext';

const Search: FC = () => {
    const { product } = useAppSelector((state) => state.menu);
    const [searchText, setSearchText] = useState('');
    const [filteredProducts, setFilteredProducts] = useState<GroupedProducts>({});
    const { languageSlug } = useLanguage();
    useEffect(() => {
        const timeout = setTimeout(() => {
            filterProducts();
        }, 100);

        return () => {
            clearTimeout(timeout);
        };
    }, [searchText]);

    const groupedProducts = product?.reduce((groups: GroupedProducts, product: Product) => {
        const { name } = product;

        if (groups[name]) {
            groups[name].push(product);
        } else {
            groups[name] = [product];
        }

        return groups;
    }, {});

    const filterProducts = () => {
        if (!groupedProducts) {
            return;
        }

        const filteredKeys = Object.keys(groupedProducts).filter((key) =>
            key.toLowerCase().includes(searchText.toLowerCase()),
        );

        const filteredGroupedProducts = filteredKeys.reduce((result: GroupedProducts, key: string) => {
            result[key] = groupedProducts[key];
            return result;
        }, {});

        setFilteredProducts(filteredGroupedProducts);
    };

    return (
        <div className='search'>
            <div className='search_header'>
                <NavLink to='/'>
                    <img className='search_header_back' src={backImg} alt='back' />
                </NavLink>
                <div className='search_input_wrapper'>
                    <input
                        id='search'
                        type='text'
                        placeholder={SearchTr.placeholder[languageSlug]}
                        value={searchText}
                        onChange={(e) => setSearchText(e.target.value)}
                    />
                </div>
            </div>
            <div className='menu'>
                {Object.values(filteredProducts).map((product, index) => (
                    <MenuCard key={index} product={product} />
                ))}
            </div>
        </div>
    );
};

export default Search;
