import { FC, useEffect } from 'react';
import { orderWorkerColumn } from './workerColumn';
import { Order, getOrderForWorker } from '../../../store/slices/orderSlice';
import UseTable from '../../../useHook/UseTable/UseTable';
import { useAppDispatch } from '../../../useHook/useRedux';

interface WorkerOrdersTableProps {
    data: Order[];
}
const WorkerOrdersTable: FC<WorkerOrdersTableProps> = ({ data }) => {
    const dispatch = useAppDispatch();

    useEffect(() => {
        dispatch(getOrderForWorker());
    }, []);

    return <>{data && <UseTable style='worker' link={false} columns={orderWorkerColumn} data={data} />}</>;
};

export default WorkerOrdersTable;
