import { FC, useState } from 'react';
import user_icon from '../../../style/image/icon_user.png';
import { logout } from '../../../store/slices/userSlice';
import { useAppDispatch } from '../../../useHook/useRedux';
import { useNavigate } from 'react-router-dom';
const AdminHeaderUserIcon: FC = () => {
    const [modal, setModal] = useState(false);

    const dispatch = useAppDispatch();
    const navigate = useNavigate();
    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };

    return (
        <>
            <img className='adminHeader_user' onClick={onModal} src={user_icon} alt='user icon' />
            {modal && (
                <div className='modalAdminHeader' onClick={closeModal}>
                    <div className='modalAdminHeader_dialog' onClick={(e) => e.stopPropagation()}>
                        <button
                            className='modalAdminHeader_logout'
                            onClick={() => {
                                dispatch(logout());
                                navigate('/');
                            }}>
                            Logout
                        </button>
                    </div>
                </div>
            )}
        </>
    );
};

export default AdminHeaderUserIcon;
