import { FC, useState, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import UseModal from '../../../../../useHook/useModal/UseModal';
import { getAddition, addAdditionToProduct, Addition } from '../../../../../store/slices/menuSlice';
import { additionColumn } from '../columnsMenu';
import ButtonAdmin from '../../ButtonAdmin';
interface ProductAddStockProps {
    productAddition?: Addition[];
}
const ProductAddAddition: FC<ProductAddStockProps> = ({ productAddition }) => {
    const [modal, setModal] = useState(false);
    const dispatch = useAppDispatch();
    const additions = useAppSelector((state) => state.menu.addition);
    const productAdditionIds = productAddition?.map((item) => item.id);
    const filteredAdditions = additions.filter((item) => !productAdditionIds?.includes(item.id));

    useEffect(() => {
        dispatch(getAddition());
    }, []);
    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };

    return (
        <>
            <ButtonAdmin text='Add Addition' handle={onModal} />
            <UseModal
                visible={modal}
                title={`Add Addition`}
                content={
                    <>
                        <UseTable
                            columns={additionColumn}
                            data={filteredAdditions}
                            link={false}
                            rel={true}
                            addRel={true}
                            addFunc={addAdditionToProduct}
                        />
                    </>
                }
                onClose={closeModal}
            />
        </>
    );
};

export default ProductAddAddition;
