import React, { FC } from 'react';
import fireIcon from '../../../../style/image/fire.png';
import { useAppDispatch, useAppSelector } from '../../../../useHook/useRedux';
import { setActiveCategory } from '../../../../store/slices/menuSlice';

interface NavCardProps {
    id: number;
    name: string;
    img_path?: string;
}

const NavCard: FC<NavCardProps> = ({ id, name, img_path }) => {
    const dispatch = useAppDispatch();
    const activeCategory = useAppSelector((state) => state.menu.activeCategory);

    const activeToggle = () => {
        dispatch(setActiveCategory({ id, name }));
    };

    const cardStyle = activeCategory.id === id ? { backgroundColor: '#0E9594' } : {};

    return (
        <div data-id={id} onClick={activeToggle} style={cardStyle} className='navMenu_item'>
            <img src={img_path} alt='fire' />
            <span>{name}</span>
        </div>
    );
};

export default NavCard;
