import React, { FC, useEffect, useState } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { getStock, delStock } from '../../../../../store/slices/menuSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import StockCreate from './StockCreate';
import { stockColumn } from '../columnsMenu';
import './../adminMenu.css';
const StockMain: FC = () => {
    const dispatch = useAppDispatch();
    const stockData = useAppSelector((state) => state.menu.stock);
    useEffect(() => {
        dispatch(getStock());
    }, []);

    return (
        <>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Stock</span>
                <StockCreate />
            </div>

            {stockData && <UseTable link={true} columns={stockColumn} data={stockData} delFunc={delStock} />}
        </>
    );
};

export default StockMain;
