import React, { FC } from 'react';
import immBasket from '../../style/image/basket.png';
import { useAppSelector } from '../../useHook/useRedux';
import { NavLink } from 'react-router-dom';
import './basket.css';
import { useLanguage } from '../../utils/LanguageContext';
interface BasketIconProps {
    header?: boolean;
}
const BasketIcon: FC<BasketIconProps> = ({ header }) => {
    const count = useAppSelector((state) => state.order.basket).reduce((total, basket) => total + basket.count, 0);
    const { languageSlug } = useLanguage();

    return (
        <>
            {count > 0 ? (
                <NavLink to={`/${languageSlug}/basket`} className={header ? 'basketIcon_header' : 'basketIcon'}>
                    <div>
                        <img src={immBasket} alt='' />
                        <span className='basketIcon_count'>{count}</span>
                    </div>
                </NavLink>
            ) : (
                <div className={header ? 'basketIcon_header' : 'basketIcon'}>
                    <div>
                        <img src={immBasket} alt='' />
                        <span className='basketIcon_count'>{count}</span>
                    </div>
                </div>
            )}
        </>
    );
};

export default BasketIcon;
