import { FC, useState } from 'react';
import { useAppSelector, useAppDispatch } from '../../../../../../useHook/useRedux';
import {
    sendOrder,
    clearBasket,
    Basket,
    setOrderSend,
    setOrderWorkerTotal,
} from '../../../../../../store/slices/orderSlice';
import WorkerMobileBasketCard from './WorkerMobileBasketCard';
import { useNavigate } from 'react-router-dom';
import back from '../../../../../../style/image/back_black.png';
import { useLanguage } from '../../../../../../utils/LanguageContext';

const WorkerMobileBasket: FC = () => {
    const dispatch = useAppDispatch();
    const { languageSlug } = useLanguage();
    const navigate = useNavigate();
    const basket = useAppSelector((state) => state.order.basket);
    const totalPrice = basket.reduce(
        (total, item) => total + item.price + item.additions.reduce((total, item) => total + item.price, 0),
        0,
    );

    const sendOrder = () => {
        const orderData = basket.map((item) => {
            return {
                count: item.count,
                product_id: item.product.id,
                addition_ids: item.additions.map(({ id }) => id),
            };
        });
        dispatch(setOrderSend(orderData));
        dispatch(setOrderWorkerTotal(totalPrice));
        navigate(`/${languageSlug}/payment`);
    };
    return (
        <>
            <div className='basket_header'>
                <div onClick={() => navigate(-1)}>
                    <img src={back} alt='' />
                </div>

                <span>Kорзина</span>
            </div>

            <div className='basket_card_container' style={{ height: 'calc(100vh - 93px - 165px)' }}>
                {basket.map((basket, index) => (
                    <WorkerMobileBasketCard
                        key={basket.keyInOrder}
                        keyInOrder={basket.keyInOrder}
                        product={basket.product}
                        additions={basket.additions}
                        price={basket.price}
                        count={basket.count}
                    />
                ))}
            </div>

            <div className='basket_bottom'>
                <button onClick={() => sendOrder()}>Оформить заказ: {totalPrice} грн.</button>
                <button onClick={() => dispatch(clearBasket())}>отменить</button>
            </div>
        </>
    );
};

export default WorkerMobileBasket;
