import { FC, useEffect, useState } from 'react';
import icon_add from '../../../../style/image/icon_add.png';
import WorkerBasketDrop from './WorkerBasketDrop';
import { Addition, Product } from '../../../../store/slices/menuSlice';
import { useAppDispatch } from '../../../../useHook/useRedux';
import { delItemBasketWorker } from '../../../../store/slices/orderSlice';
interface WorkerBasketCardProps {
    products: Product[];
    indexProduct: number;
    keyInBasket: string;
    additionsСhosen?: Addition[];
    handleOrder: (key: string, selectedProduct: { product: Product; count: number }) => void;
    delItemOrderMethod: (key: string) => void;
    handlerAddition: (on: boolean, key: string) => void;
}
const WorkerBasketCard: FC<WorkerBasketCardProps> = ({
    products,
    indexProduct,
    keyInBasket,
    additionsСhosen,
    handleOrder,
    delItemOrderMethod,
    handlerAddition,
}) => {
    const [productAmount, setProductAmount] = useState<string>(products[0].amount);
    const [count, setCount] = useState(1);

    const dispatch = useAppDispatch();
    const selectedProduct = products.find((item) => item?.amount === productAmount) as Product;
    useEffect(() => {
        const selected = { product: selectedProduct, count, additions: additionsСhosen };
        handleOrder(keyInBasket, selected);
    }, [selectedProduct, count]);
    const handleAmount = (amount: string) => {
        setProductAmount(amount);
    };

    const handleCounter = (operation: string) => {
        if (operation === 'increment') {
            setCount(count + 1);
        } else if (operation === 'decrement') {
            setCount(count - 1);
        }
    };

    useEffect(() => {
        if (count === 0) {
            delItemOrderMethod(keyInBasket);
            dispatch(delItemBasketWorker(indexProduct));
        }
    }, [count]);

    return (
        <div className='worker_basket_products_card'>
            <div className='worker_basket_products_card_wrapper'>
                <span className='worker_basket_products_card_name'>{selectedProduct.name}</span>
                <div className='worker_basket_products_card_counter'>
                    <span onClick={() => handleCounter('decrement')}>-</span>
                    <span>{count}</span>
                    <span onClick={() => handleCounter('increment')}>+</span>
                </div>

                <WorkerBasketDrop
                    activeAmount={productAmount}
                    setAmount={handleAmount}
                    amount={products.map(({ amount }) => amount)}
                />

                <span className='worker_basket_products_card_price'>{selectedProduct.price * count} грн.</span>
            </div>
            <div className='worker_basket_products_card_add' onClick={() => handlerAddition(true, keyInBasket)}>
                <img src={icon_add} alt='icon add' />
                <span>Add</span>
            </div>
            {additionsСhosen &&
                additionsСhosen.length > 0 &&
                additionsСhosen.map((addit) => (
                    <div key={addit.id} className='worker_basket_products_card_addition'>
                        <span>{addit.name}</span>
                        <span>{addit.price * count} грн.</span>
                    </div>
                ))}
        </div>
    );
};

export default WorkerBasketCard;
