import { createSlice, PayloadAction, createAsyncThunk } from '@reduxjs/toolkit';
import $api from '../../axios';
import { Product } from './menuSlice';
import { Addition } from './menuSlice';
import { GroupedProducts } from '../../defaultUser/menu/Menu';

export interface Basket {
    keyInOrder: string | number;
    product: Product;
    count: number;
    price: number;
    additions: Addition[];
}
export interface Order {
    id: number;
    Products: Product[];
    status: string;
    payment: string;
    count: number;
    price: number;
    updatedBy: number | null;
    createdAt: string;
    updatedAt: string;
    comment: string;
    serializedData: string;
    user_id: string | null;
    Additions: Addition[];
    [key: string]: any;
}

export interface OrderPayloadJsonSend {
    product_id: number;
    count: number;
    addition_ids: number[];
}
export interface OrderPayload {
    data: OrderPayloadJsonSend[];
    payment: string;
    comment?: string;
    status?: string;
}
export const sendOrder = createAsyncThunk<number, OrderPayload>(
    'order/sendOrder',
    async ({ data, payment, status }, { rejectWithValue, dispatch }) => {
        try {
            const req = await $api.post('/order', {
                data,
                payment,
                status,
            });
            return req.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);
export const sendOrderUser = createAsyncThunk<number, OrderPayload>(
    'order/sendOrder',
    async ({ data, payment, comment }, { rejectWithValue, dispatch }) => {
        try {
            const req = await $api.post('/order', {
                data,
                payment,
            });
            dispatch(setUserOrderStore(req.data.order));
            return req.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);
export const getOrder = createAsyncThunk<void>('menu/getOrder', async (_, { rejectWithValue, dispatch }) => {
    try {
        const response = await $api.get('/order/admin');

        dispatch(setOrder(response.data.orders));
    } catch (error) {
        return rejectWithValue((error as Error).message);
    }
});
export const getOrderForWorker = createAsyncThunk<void>('menu/getOrder', async (_, { rejectWithValue, dispatch }) => {
    try {
        const response = await $api.get('/worker/order');

        dispatch(setOrder(response.data.orders));
    } catch (error) {
        return rejectWithValue((error as Error).message);
    }
});

export const putOrder = createAsyncThunk<number, { formData: FormData; id: number }>(
    'menu/putOrder',
    async ({ formData, id }, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.put(`/order/admin/${id}`, formData);

            dispatch(getOrder());
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);
export const putOrderWorker = createAsyncThunk<number, { formData: { status: string }; id: number }>(
    'menu/putOrder',
    async ({ formData, id }, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.put(`/worker/order/status/${id}`, formData);

            dispatch(getOrderForWorker());
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);

export const deleteAdditionFromProductOrder = createAsyncThunk<number, any>(
    'menu/deleteAdditionFromProductOrder',
    async ({ id, product_id, addition_id }, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.delete(
                `/order/admin/addition/${id}?product_id=${product_id}&addition_id=${addition_id}`,
            );
            dispatch(getOrder());
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);

export const deleteProductFromOrder = createAsyncThunk<{ status: number; order: any }, any>(
    'menu/deleteProductFromOrder',
    async ({ id, product_id }, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.delete(`/order/admin/product/${id}?product_id=${product_id}`);
            dispatch(getOrder());
            return { status: response.status, order: response.data.order };
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);

interface State {
    order: Order[];
    userOrderStore: Order | null;
    sendData: OrderPayloadJsonSend[];
    basket: Basket[];
    orderWorkerTotal: number;
    basketWorker: GroupedProducts[];
    loading: boolean;
    error: string | null;
}

const initialState: State = {
    order: [],
    userOrderStore: null,
    sendData: [],
    basket: [],
    orderWorkerTotal: 0,
    basketWorker: [],
    loading: false,
    error: null,
};

const orderSlice = createSlice({
    name: 'order',
    initialState,
    reducers: {
        addOrderData(state, action: PayloadAction<Basket>) {
            state.basket.push(action.payload);
        },
        clearBasket(state) {
            state.basket = [];
        },
        updateOrderData(state, action: PayloadAction<Basket>) {
            const { product, count, price, additions, keyInOrder } = action.payload;
            const existingOrder = state.basket.find((basket) => basket.keyInOrder === keyInOrder);
            if (existingOrder) {
                existingOrder.product = product;
                existingOrder.count = count;
                existingOrder.price = price;
                existingOrder.additions = additions;
            }
        },
        delItemBasket(state, action: PayloadAction<string | number>) {
            const keyToDelete = action.payload;
            const newBasket = state.basket.filter((basket) => basket.keyInOrder !== keyToDelete);
            state.basket = newBasket;
        },
        setOrder(state, action: PayloadAction<Order[]>) {
            state.order = action.payload;
        },
        setOrderSend(state, action: PayloadAction<OrderPayloadJsonSend[]>) {
            state.sendData = action.payload;
        },
        setBasketWorker(state, action: PayloadAction<GroupedProducts>) {
            state.basketWorker.push(action.payload);
        },
        setUserOrderStore(state, action: PayloadAction<Order | null>) {
            state.userOrderStore = action.payload;
        },

        setOrderWorkerTotal(state, action: PayloadAction<number>) {
            state.orderWorkerTotal = action.payload;
        },
        clearBasketAndOrderWorker(state) {
            state.basket = [];
            state.basketWorker = [];
            state.orderWorkerTotal = 0;
        },

        delItemBasketWorker(state, action: PayloadAction<number>) {
            const indexToDelete = action.payload;
            const newBasketWorker = state.basketWorker.filter((_, index) => index !== indexToDelete);
            state.basketWorker = newBasketWorker;
        },
    },
});

export const {
    addOrderData,
    updateOrderData,
    setOrder,
    setBasketWorker,
    delItemBasketWorker,
    clearBasketAndOrderWorker,
    clearBasket,
    delItemBasket,
    setOrderSend,
    setOrderWorkerTotal,
    setUserOrderStore,
} = orderSlice.actions;

export default orderSlice.reducer;
