import { FC } from 'react';
import { useAppSelector } from '../../../useHook/useRedux';
import { useParams } from 'react-router-dom';
import WorkerMenuCard from './menuCard/WorkerMenuCard';
import { Product } from '../../../store/slices/menuSlice';
import WorkerBasket from './basket/WorkerBasket';
import WorkerMenuHeader from './WorkerMenuHeader';
export interface GroupedProducts {
    [name: string]: Product[];
}
const WorkerMenuProduct: FC = () => {
    const { category } = useParams();

    const products = useAppSelector((state) => state.menu.menu).find((cat) => cat.name === category)
        ?.Products as Product[];
    const groupedProducts: GroupedProducts = products.reduce((groups: GroupedProducts, product: Product) => {
        const { name } = product;

        if (groups[name]) {
            groups[name].push(product);
        } else {
            groups[name] = [product];
        }

        return groups;
    }, {});

    return (
        <div className='worker_menu_wrapper'>
            <div className='worker_menu'>
                <WorkerMenuHeader />
                <span className='worker_menu_title'>{category}</span>
                <div className='worker_menu_cards '>
                    {groupedProducts &&
                        Object.entries(groupedProducts).map(([key, prod]) => (
                            <WorkerMenuCard
                                key={prod[0].name}
                                title={prod[0].name}
                                img_src={prod[0].img_path}
                                products={{ [key]: prod }}
                            />
                        ))}
                </div>
            </div>

            <WorkerBasket />
        </div>
    );
};

export default WorkerMenuProduct;
