import { FC, useState } from 'react';
import { useAppDispatch } from '../../../../../useHook/useRedux';
import { createAdmin, User } from '../../../../../store/slices/userAdminSlice';
import UseModal from '../../../../../useHook/useModal/UseModal';
import UseForm from '../../../../../useHook/useForm/useForm';
import ButtonAdmin from '../../ButtonAdmin';
interface modalCreateType {
    visible: boolean;
    item: User | null;
}
const UserAdminCreate: FC = () => {
    const [modalCreate, setModalCreate] = useState<modalCreateType>({
        visible: false,
        item: null,
    });
    const dispatch = useAppDispatch();

    const closeModal = () =>
        setModalCreate(() => {
            return {
                item: null,
                visible: false,
            };
        });

    const onModal = () => {
        setModalCreate(() => {
            return {
                item: null,
                visible: true,
            };
        });
    };
    const submitCreate = async (formData: FormData) => {
        const req = await dispatch(createAdmin(formData));
        if (req.payload === 200) closeModal();
    };
    const initialNames = {
        name: 'name',
        login: 'login',
        password: 'password',
    };

    const initialValues = {
        name: '',
        login: '',
        password: '',
    };
    return (
        <>
            <ButtonAdmin text='Create Admin' handle={onModal} />

            <UseModal
                visible={modalCreate.visible}
                title='Create Admin'
                content={
                    <UseForm
                        initialNames={initialNames}
                        initialValues={initialValues}
                        subminFunc={submitCreate}
                        handleForm={closeModal}
                        img={false}
                    />
                }
                onClose={closeModal}
            />
        </>
    );
};

export default UserAdminCreate;
