import { FC } from 'react';
import { useAppSelector } from '../../../../../useHook/useRedux';
import { useParams } from 'react-router-dom';
import { stockColumn } from '../columnsMenu';
import StockPut from './StockPut';

const StockDetails: FC = () => {
    const { id } = useParams();

    const stock = useAppSelector((state) => state.menu.stock).filter((stock) => stock.id == parseInt(id ? id : ''))[0];

    return (
        <div>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Stock</span>
                <StockPut
                    id={stock.id}
                    name={stock.name}
                    amount={stock.amount}
                    quantity={stock.quantity}
                    price={stock.price}
                />
            </div>

            <div className='adminDetails_item_wrapper'>
                {stockColumn.map((i) => {
                    if (!stock[i.accessor]) return;
                    return (
                        <div className='adminDetails_item'>
                            <span>{i.header}: </span>
                            <span> {stock[i.accessor]}</span>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default StockDetails;
