import { FC } from 'react';
import UseModal from '../../../useHook/useModal/UseModal';
import { Order } from '../../../store/slices/orderSlice';
import './workerOrder.css';
import useWindowDimensions from '../../../utils/useWindowDimensions';
interface OrderModalForTableProps {
    onClose: () => void;
    order: Order;
}
const OrderModalForTable: FC<OrderModalForTableProps> = ({ onClose, order }) => {
    const { widthCreen } = useWindowDimensions();
    return (
        <UseModal
            classStyle={widthCreen && widthCreen < 500 ? 'workerOrder' : ''}
            content={
                <div className='orderWorkerDetails_modal'>
                    <div className='orderWorkerDetails_modal_top'>
                        <div>
                            <span className='orderWorkerDetails_modal_top_bold'>Order №{order.num}</span>
                        </div>
                        <div>
                            Time: <span className='orderWorkerDetails_modal_top_bold'>{order.createdAt}</span>
                        </div>
                        <div>
                            Status: <span className='orderWorkerDetails_modal_top_payment'>{order.payment}</span>
                        </div>
                    </div>
                    {order.Products.length > 0 && (
                        <div className='orderWorkerDetails_modal_wrapper'>
                            <div className='orderWorkerDetails_modal_main'>
                                <div className='orderWorkerDetails_modal_main_head'>
                                    <span>№</span>
                                    <span>Product</span>
                                    <span>Q-ty</span>
                                    <span>Amount</span>
                                    <span>Total price</span>
                                </div>
                                <div className='orderWorkerDetails_modal_main_body'>
                                    {order.Products.map((prod, index) => {
                                        return (
                                            <div key={index} className='orderWorkerDetails_modal_main_body_card'>
                                                <div className='orderWorkerDetails_modal_main_body_card_prod'>
                                                    <span>{index + 1}</span>
                                                    <span>{prod.name}</span>
                                                    <span>{prod.ProoductsOrder.count}</span>
                                                    <span>{prod.amount}</span>
                                                    <span>
                                                        {(prod.price +
                                                            prod.Additions.reduce(
                                                                (start, item) => start + item.price,
                                                                0,
                                                            )) *
                                                            prod.ProoductsOrder.count}
                                                    </span>
                                                </div>
                                                {prod.Additions.length > 0 && (
                                                    <div className='orderWorkerDetails_modal_main_body_card_addit'>
                                                        <span>Additions:</span>

                                                        {prod.Additions.map((add) => (
                                                            <span>{add.name},</span>
                                                        ))}
                                                    </div>
                                                )}
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        </div>
                    )}

                    {order.comment && (
                        <div className='orderWorkerDetails_modal_comment'>
                            <span>Comment:</span>
                            <span>{order.comment}</span>
                        </div>
                    )}
                </div>
            }
            visible={true}
            onClose={onClose}
        />
    );
};

export default OrderModalForTable;
