import { FC } from 'react';
import { useParams } from 'react-router-dom';
import { removePermissionFromAdmin } from '../../../../../store/slices/userAdminSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import { useAppSelector } from '../../../../../useHook/useRedux';
import AddPermissionToAdmin from '../admin/AddPermissionToAdmin';
import { userDeteils, permissionColumn } from '../columnUsers';

const UserWorkerDetails: FC = () => {
    const { id } = useParams();
    const worker = useAppSelector((state) => state.adminUser.worker).filter(
        (worker) => worker.id === parseInt(id ? id : ''),
    )[0];

    return (
        <div>
            <div className='adminDetails_item_wrapper'>
                {userDeteils.map((i) => {
                    if (!worker[i.accessor]) return;
                    return (
                        <div key={i.accessor} className='adminDetails_item'>
                            <span>{i.header}: </span>
                            <span> {worker[i.accessor]}</span>
                        </div>
                    );
                })}
            </div>

            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Permission:</span>
                <AddPermissionToAdmin permissionUser={worker.Permissions} />
            </div>

            {worker?.Permissions && (
                <>
                    <UseTable
                        columns={permissionColumn}
                        data={worker.Permissions}
                        link={false}
                        delFunc={removePermissionFromAdmin}
                        rel={true}
                    />
                </>
            )}
        </div>
    );
};

export default UserWorkerDetails;
