import { FC, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../useHook/useRedux';
import { getMenu } from '../../../store/slices/menuSlice';
import WorkerMenuCard from './menuCard/WorkerMenuCard';
import WorkerBasket from './basket/WorkerBasket';
import WorkerMenuHeader from './WorkerMenuHeader';
import './workerMenu.css';
const WorkerMenuCategory: FC = () => {
    const dispatch = useAppDispatch();

    const categories = useAppSelector((state) => state.menu.menu);

    useEffect(() => {
        dispatch(getMenu());
    }, []);
    return (
        <div className='worker_menu_wrapper'>
            <div className='worker_menu'>
                <WorkerMenuHeader />
                <span className='worker_menu_title'>Category</span>
                <div className='worker_menu_cards mobile'>
                    {categories &&
                        categories.map((cateory) => (
                            <WorkerMenuCard
                                key={cateory.id}
                                title={cateory.name}
                                img_src={cateory?.img_path}
                                to={cateory.name}
                            />
                        ))}
                </div>
            </div>

            <WorkerBasket />
        </div>
    );
};

export default WorkerMenuCategory;
