import { FC, useState } from 'react';
import WorkerOrdersTable from './WorkerOrdersTable';
import WorkerMenuHeader from '../menu/WorkerMenuHeader';
import { useAppSelector } from '../../../useHook/useRedux';
import './workerOrder.css';
import { formatDateTimeString } from '../../../utils/changeData';
const WorkerOrdersMain: FC = () => {
    const [filteredData, setFilteredData] = useState('process');
    const orderLength = useAppSelector((state) => state.order.order).length;
    const orderData = useAppSelector((state) => state.order.order).map(({ updatedAt, createdAt, ...rest }) => ({
        updatedAt: updatedAt === createdAt ? '' : formatDateTimeString(updatedAt),
        createdAt: formatDateTimeString(createdAt),
        ...rest,
    }));
    const orderFilterData = orderData.filter((item) => item.status === filteredData);
    const orderFilterProcess = orderData.filter((item) => item.status === 'process').length;
    const orderFilterPending = orderData.filter((item) => item.status === 'pending').length;
    const handlFilter = (arg: string) => setFilteredData(arg);
    return (
        <div className='worker_orders_mainWrapper'>
            <WorkerMenuHeader />

            <div className='workerOrders_tile_wrapper'>
                <div className='workerOrders_tile_order'>
                    <span>Orders: </span>
                    <span>{orderLength && orderLength}</span>
                </div>
                {/* <div className='workerOrders_tile_status'>
                    <span>Status:</span>
                    <span>Order paid</span>
                    <span>Order in process</span>
                </div> */}
                <div className='worker_orders_filter'>
                    <span
                        className={filteredData === 'process' ? 'active' : undefined}
                        onClick={() => handlFilter('process')}>
                        Process : {orderFilterProcess}
                    </span>
                    <span
                        className={filteredData === 'pending' ? 'active' : undefined}
                        onClick={() => handlFilter('pending')}>
                        Pending : {orderFilterPending}
                    </span>
                </div>
            </div>

            <WorkerOrdersTable data={orderFilterData} />
        </div>
    );
};

export default WorkerOrdersMain;
