import { FC, useEffect } from 'react';
import WorkerRoutes from './components/WorkerRoutes';
import axios, { API_URL, RefreshPayload } from '../axios';
import { fetchEventSource } from '@microsoft/fetch-event-source';
import { useAppDispatch } from '../useHook/useRedux';
import { getOrderForWorker } from '../store/slices/orderSlice';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const MainWorker: FC = () => {
    const dispatch = useAppDispatch();
    const handleNewOrder = () => {
        toast.info('Новый заказ поступил!', {
            position: toast.POSITION.TOP_RIGHT,
            autoClose: false,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
        });

        const audio = new Audio('https://drive.google.com/uc?export=download&id=1M95VOpto1cQ4FQHzNBaLf0WFQglrtWi7');
        audio.play();
    };
    useEffect(() => {
        const token = localStorage.getItem('token');
        const headers = {
            Authorization: `Bearer ${token}`,
        };
        const controller = new AbortController();
        const connectEventSource = async () => {
            await fetchEventSource(`${API_URL}/connect_order_worker`, {
                headers,
                signal: controller.signal,
                async onopen(response) {
                    if (response.status === 401) {
                        const response = await axios.get<RefreshPayload>(`${API_URL}/refresh`, {
                            withCredentials: true,
                        });
                        localStorage.setItem('token', response.data.acsses);
                    }
                },
                onmessage() {
                    dispatch(getOrderForWorker());
                    handleNewOrder();
                },
            });
        };
        connectEventSource();
        return () => controller.abort();
    }, []);

    return (
        <>
            <ToastContainer />
            <WorkerRoutes />
        </>
    );
};

export default MainWorker;
