import { FC, useEffect } from 'react';
import { getWorker } from '../../../../../store/slices/userAdminSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import UserWorkerCreate from './UserWorkerCreate';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { userColumn } from '../columnUsers';
import { delUser } from '../../../../../store/slices/userAdminSlice';

const UserWorker: FC = () => {
    const dispatch = useAppDispatch();
    const workerData = useAppSelector((state) => state.adminUser.worker);
    useEffect(() => {
        dispatch(getWorker());
    }, []);

    return (
        <>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Worker</span>
                <UserWorkerCreate />
            </div>

            {workerData && <UseTable columns={userColumn} link={true} data={workerData} delFunc={delUser} />}
        </>
    );
};

export default UserWorker;
