import { useEffect, FC } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { getProduct, delProduct } from '../../../../../store/slices/menuSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import ProductCreate from './ProductCreate';
import { productColumn } from '../columnsMenu';
import './../adminMenu.css';
const ProductMain: FC = () => {
    const dispatch = useAppDispatch();
    const productData = useAppSelector((state) => state.menu.product);
    useEffect(() => {
        dispatch(getProduct());
    }, []);

    return (
        <>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Product</span>
                <ProductCreate />
            </div>

            {productData && <UseTable columns={productColumn} data={productData} link={true} delFunc={delProduct} />}
        </>
    );
};

export default ProductMain;
