import { createContext, useContext, useState, ReactNode } from 'react';
import { Navigate, Route, Routes } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../useHook/useRedux';
import { setLang } from '../store/slices/userSlice';
interface LanguageContextType {
    languageSlug: string;
    changeLanguageSlug: (slug: string) => void;
}

const LanguageContext = createContext<LanguageContextType | undefined>(undefined);

interface LanguageProviderProps {
    children: ReactNode;
}

export function LanguageProvider({ children }: LanguageProviderProps) {
    const dispatch = useAppDispatch();
    const languageSlug = useAppSelector((state) => state.user.lang);

    const changeLanguageSlug = (newSlug: string) => {
        dispatch(setLang(newSlug));
    };

    return (
        <Routes>
            <Route
                path={`/${languageSlug}/*`}
                element={
                    <LanguageContext.Provider value={{ languageSlug, changeLanguageSlug }}>
                        {children}
                    </LanguageContext.Provider>
                }
            />
            <Route path='*' element={<Navigate to={`/${languageSlug}/`} />} />
        </Routes>
    );
}

export function useLanguage() {
    const context = useContext(LanguageContext);
    if (context === undefined) {
        throw new Error('useLanguage must be used within a LanguageProvider');
    }
    return context;
}
