import { FC, useState } from 'react';
import NavMenu from './navMenu/NavMenu';
import { Twirl as HamburgerIcon } from 'hamburger-react';
import Hamburger from '../hamburger/Hamburger';
import { NavLink } from 'react-router-dom';
import logoIcon from '../../style/image/logo.png';
import LangSlugHeader from './LangSlugHeader';
import searchIcon from '../../style/image/search.png';
import useWindowDimensions from '../../utils/useWindowDimensions';
import './header.css';
import BasketIcon from '../basket/BasketIcon';
import { useLanguage } from '../../utils/LanguageContext';
interface HeaderClientProps {
    nav?: boolean;
}
const HeaderClient: FC<HeaderClientProps> = ({ nav = true }) => {
    const [isOpenHamburger, setIsOpenHamburger] = useState(false);
    const { widthCreen } = useWindowDimensions();
    const closeModalHamburger = () => {
        setIsOpenHamburger(false);
    };
    const { languageSlug } = useLanguage();

    return (
        <header style={!nav ? { marginBottom: '0' } : undefined}>
            <div className='header'>
                <div className='header_logo_container'>
                    <img src={logoIcon} alt='logo' />
                </div>
                <div className='header_right'>
                    <NavLink to={`/${languageSlug}/search`} className='NavLink'>
                        <img src={searchIcon} alt='search icon' />
                    </NavLink>
                    <LangSlugHeader />
                    {widthCreen && widthCreen > 900 && <BasketIcon header={true} />}
                    <HamburgerIcon toggled={isOpenHamburger} toggle={setIsOpenHamburger} />
                    <Hamburger lang={languageSlug} isOpen={isOpenHamburger} closeModal={closeModalHamburger} />
                </div>
            </div>
            {nav && <NavMenu />}
        </header>
    );
};

export default HeaderClient;
