// Product and Category ======================================
export interface Category {
    id: number;
    name: string;
    img_path?: string;
    Products?: Product[];
    [key: string]: any;
}
export const getCategory = createAsyncThunk<void>('menu/getCategory', async (_, { rejectWithValue, dispatch }) => {
    try {
        const response = await $api.get('/category');

        dispatch(setCategory(response.data.data));
    } catch (error) {
        return rejectWithValue((error as Error).message);
    }
});
export const createCategory = createAsyncThunk<number, FormData>(
    'menu/createCategory',
    async (formData, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.post('/category', formData);

            dispatch(getMenu());
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);
