export interface Product {
    ProoductsOrder?: any;
    id: number;
    name: string;
    amount: string;
    price: number;
    description: string;
    Additions: Addition[];
    Stocks?: Stock[];
    Categories?: Category[];
    img_path?: string;
    [key: string]: any;
}
export const getMenu = createAsyncThunk<void>('menu/getMenu', async (_, { rejectWithValue, dispatch }) => {
    try {
        const response = await $api.get('/category');

        dispatch(setMenu(response.data.data));

        const uniqueProductIds = new Set();
        const products: Product[] = response.data.data.flatMap(
            (category: Category) =>
                category.Products?.filter((product: Product) => {
                    if (!uniqueProductIds.has(product.id)) {
                        uniqueProductIds.add(product.id);
                        return true;
                    }
                    return false;
                }),
        );

        dispatch(setProduct(products));
        const categories: Category[] = response.data.data.map(({ id, name, img_path }: Category) => ({
            id,
            name,
            img_path,
        }));

        dispatch(setCategory(categories));
        dispatch(setActiveCategory({ id: categories[0].id }));
    } catch (error) {
        return rejectWithValue((error as Error).message);
    }
});
