interface State {
    category: Category[];
    activeCategory: { id: number; name?: string };
    menu: Category[];
    product: Product[];
    addition: Addition[];
    stock: Stock[];
    loading: boolean;
    error: string | null;
}

const initialState: State = {
    category: [],
    activeCategory: { id: 0 },
    menu: [],
    product: [],
    addition: [],
    stock: [],
    loading: false,
    error: null,
};

const menuSlice = createSlice({
    name: 'menu',
    initialState,
    reducers: {
        setMenu(state, action: PayloadAction<Category[]>) {
            state.menu = action.payload;
        },
        setProduct(state, action: PayloadAction<Product[]>) {
            state.product = action.payload;
        },
        setCategory(state, action: PayloadAction<Category[]>) {
            state.category = action.payload;
        },
        setAddition(state, action: PayloadAction<Addition[]>) {
            state.addition = action.payload;
        },
        setStock(state, action: PayloadAction<Stock[]>) {
            state.stock = action.payload;
        },
        setActiveCategory(state, action: PayloadAction<any>) {
            state.activeCategory = action.payload;
        },
    },
    extraReducers: (builder) => {
        builder
            .addCase(getMenu.pending, (state) => {
                state.loading = true;
                state.error = null;
            })
            .addCase(getMenu.fulfilled, (state) => {
                state.loading = false;
                state.error = null;
            })
            .addCase(getMenu.rejected, (state, action) => {
                state.loading = false;
                state.error = action.payload as string;
            });
    },
});

export const { setMenu, setProduct, setCategory, setActiveCategory, setAddition, setStock } = menuSlice.actions;

export default menuSlice.reducer;
