import React, { FC, useState } from 'react';
import { useAppSelector, useAppDispatch } from '../../useHook/useRedux';
import { Basket, delItemBasket } from '../../store/slices/orderSlice';
import ModalMobileForProduct from '../menu/modalForCard/ModalMobileForProduct';
import ModalDekstop from '../menu/modalForCard/ModalDekstop';
import cancel_icon from '../../style/image/icon_del_red.png';
import useWindowDimensions from '../../utils/useWindowDimensions';
import { useLanguage } from '../../utils/LanguageContext';
import { MenuTrDef, BasketTr, PriceTr } from '../../constatns/translateDefaultUser';
const BasketCardDekstop: FC<Basket> = ({ product, count, price, additions, keyInOrder }) => {
    const [isOpenChange, setIsOpenChange] = useState(false);
    const { widthCreen } = useWindowDimensions();
    const { languageSlug } = useLanguage();
    const products = useAppSelector((state) => state.menu.product).filter(({ name }) => name === product.name);
    const dispatch = useAppDispatch();
    const openModalChange = () => {
        setIsOpenChange(true);
    };

    const closeModalChange = () => {
        setIsOpenChange(false);
    };
    const handleCancel = () => dispatch(delItemBasket(keyInOrder));
    return (
        <div className='basket_card_dekstop'>
            <div className='basket_card_dekstop_product'>
                <img className='basket_card_dekstop_product_img' src={product.img_path} alt='product' />

                <span className='basket_card_dekstop_name'>{product.name}</span>

                <span className='basket_card_dekstop_amount'>
                    {MenuTrDef.modalAmount[product.amount][languageSlug]}
                </span>
                <span className='basket_card_dekstop_count'>{count}</span>
                <span className='basket_card_dekstop_price'>
                    {price} {PriceTr.currency[languageSlug]}
                </span>
                <button className='basket_card_dekstop_change' onClick={openModalChange}>
                    {BasketTr.card.change[languageSlug]}
                </button>
                <img
                    className='basket_card_dekstop_cancelIcon'
                    onClick={handleCancel}
                    src={cancel_icon}
                    alt='cancel icon'
                />
            </div>
            {additions.length > 0 && (
                <div className='basket_card_dekstop_additions'>
                    <span className='basket_card_dekstop_additions_title'>+</span>

                    {additions?.map((addition, index) => (
                        <span key={index} className='basket_card_dekstop_additions_card_title'>
                            {addition.name}
                            {additions.length > index + 1 && ','}
                        </span>
                    ))}
                </div>
            )}

            {widthCreen && widthCreen > 500 ? (
                <ModalDekstop
                    isOpen={isOpenChange}
                    lang={languageSlug}
                    closeModal={closeModalChange}
                    product={products}
                    order={{ product, count, price, additions, keyInOrder }}
                />
            ) : (
                <ModalMobileForProduct
                    isOpen={isOpenChange}
                    lang={languageSlug}
                    closeModal={closeModalChange}
                    product={products}
                    order={{ product, count, price, additions, keyInOrder }}
                />
            )}
        </div>
    );
};

export default BasketCardDekstop;
