import { FC, useState } from 'react';
import { useAppDispatch } from '../../../../../useHook/useRedux';
import { Category, putCategory } from '../../../../../store/slices/menuSlice';
import UseModal from '../../../../../useHook/useModal/UseModal';
import UseForm from '../../../../../useHook/useForm/useForm';
import ButtonAdmin from '../../ButtonAdmin';
interface modalCreateType {
    visible: boolean;
    item: Category | null;
}

const CategoryPut: FC<Category> = ({ id, name }) => {
    const [modalCreate, setModalCreate] = useState<modalCreateType>({
        visible: false,
        item: null,
    });
    const dispatch = useAppDispatch();

    const closeModal = () =>
        setModalCreate(() => {
            return {
                item: null,
                visible: false,
            };
        });

    const onModal = () => {
        setModalCreate(() => {
            return {
                item: null,
                visible: true,
            };
        });
    };
    const submitCreate = async (formData: FormData) => {
        if (id) {
            const req = await dispatch(putCategory({ formData, id }));
            if (req.payload === 200) closeModal();
        }
    };
    const initialNames = {
        name: 'Названиe',
    };
    const initialValues = {
        name: name,
    };
    return (
        <>
            <ButtonAdmin text='PUT Product' handle={onModal} />

            <UseModal
                visible={modalCreate.visible}
                title='Create Stock'
                content={
                    <UseForm
                        initialNames={initialNames}
                        initialValues={initialValues}
                        subminFunc={submitCreate}
                        handleForm={closeModal}
                        img={true}
                    />
                }
                onClose={closeModal}
            />
        </>
    );
};

export default CategoryPut;
