import { FC, useState } from 'react';
import { useAppSelector, useAppDispatch } from '../../../useHook/useRedux';
import WorkerMenuHeader from './WorkerMenuHeader';
import { useNavigate } from 'react-router-dom';
import cash_icon from '../../../style/image/cash_icon.png';
import card_icon from '../../../style/image/credit_card_icon.png';

import { clearBasketAndOrderWorker, sendOrder } from '../../../store/slices/orderSlice';
import useWindowDimensions from '../../../utils/useWindowDimensions';

const WorkerOrderPayment: FC = () => {
    const [payment, setPayment] = useState('');
    const { widthCreen } = useWindowDimensions();
    const navigate = useNavigate();
    const dispatch = useAppDispatch();
    const { sendData, orderWorkerTotal } = useAppSelector((state) => state.order);
    const send = async () => {
        if (payment) {
            const req = await dispatch(sendOrder({ data: sendData, payment, status: 'process' }));
            if (req.payload === 200) {
                handleCancel();
                navigate(-1);
            }
        }
    };
    const handleCancel = () => {
        dispatch(clearBasketAndOrderWorker());
        navigate(-1);
    };
    const handlePayment = (pay: string) => {
        setPayment(pay);
    };
    return (
        <div className='worker_menu_wrapper'>
            {widthCreen && widthCreen > 500 ? (
                <div className='worker_menu'>
                    <WorkerMenuHeader />
                    <div className='worker_menu_cards mobile'></div>
                </div>
            ) : (
                <WorkerMenuHeader />
            )}

            <div className='worker_basket'>
                <span className='worker_basket_header'>Payment</span>
                <div className='worker_basket_products'>
                    <div className='worker_payment_price'>
                        <span>Total price:</span>
                        <span>{orderWorkerTotal}грн.</span>
                    </div>

                    <div
                        style={payment === 'cash' ? { borderColor: '#f2542d' } : {}}
                        className='worker_payment_item'
                        onClick={() => handlePayment('cash')}>
                        <img src={cash_icon} alt='cash icon' />
                        <span>Cash</span>
                    </div>

                    <div
                        className='worker_payment_item'
                        style={payment === 'card' ? { borderColor: '#f2542d' } : {}}
                        onClick={() => handlePayment('card')}>
                        <img src={card_icon} alt='cash icon' />
                        <span>Card</span>
                    </div>
                </div>

                <div className='worker_basket_bottom'>
                    <div className='worker_basket_bottom_text'></div>
                    <div className='worker_basket_bottom_button'>
                        <button onClick={() => handleCancel()}>Cancel</button>
                        <button onClick={() => send()}>Approve</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default WorkerOrderPayment;
