import { FC, useState } from 'react';
import { Addition } from '../../../../store/slices/menuSlice';

interface WorkerBasketAdditionProps {
    additionsChosenProduct: Addition[];
    additionsСhosen?: Addition[];
    key_product_in_basket: string;
    handlerAdditionBasket: (on: boolean, key: string) => void;
    addAdditionToBasket: (key: string, addition: Addition[]) => void;
}
const WorkerBasketAddition: FC<WorkerBasketAdditionProps> = ({
    additionsChosenProduct,
    handlerAdditionBasket,
    key_product_in_basket,
    additionsСhosen,
    addAdditionToBasket,
}) => {
    const [additionsSelected, setAdditions] = useState<Addition[]>(additionsСhosen ? additionsСhosen : []);
    const handleAdditionsSelected = (item: Addition) => {
        if (additionsSelected.includes(item)) {
            setAdditions((state) => state.filter((i) => i.id !== item.id));
        } else {
            setAdditions((state) => [item, ...state]);
        }
    };

    const approveAddition = () => addAdditionToBasket(key_product_in_basket, additionsSelected);

    return (
        <div className='worker_basket'>
            <span className='worker_basket_header'>Additions</span>
            <div className='worker_basket_T-head'>
                <span>Name</span>
                <span>Add</span>
                <span>Price</span>
            </div>
            <div className='worker_basket_products'>
                {additionsChosenProduct &&
                    additionsChosenProduct.map((addit) => (
                        <div
                            key={addit.id}
                            className='worker_basket_addition'
                            onClick={() => handleAdditionsSelected(addit)}>
                            <span>{addit.name}</span>
                            {additionsSelected.includes(addit) ? <span>-</span> : <span>+</span>}
                            <span>{addit.price}</span>
                        </div>
                    ))}
            </div>

            <div className='worker_basket_bottom'>
                <div className='worker_basket_bottom_text'></div>
                <div className='worker_basket_bottom_button'>
                    <button onClick={() => handlerAdditionBasket(false, '')}>Cancel</button>
                    <button
                        onClick={() => {
                            approveAddition();
                            handlerAdditionBasket(false, '');
                        }}>
                        Approve
                    </button>
                </div>
            </div>
        </div>
    );
};

export default WorkerBasketAddition;
