import { FC } from 'react';
import WorkerMenuCategory from './WorkerMenuCategory';
import WorkerMenuMobileMain from './mobile/menu/WorkerMenuMobileMain';
import useWindowDimensions from '../../../utils/useWindowDimensions';
const WorkerMenuMain: FC = () => {
    const { widthCreen } = useWindowDimensions();

    return <div>{widthCreen && widthCreen > 650 ? <WorkerMenuCategory /> : <WorkerMenuMobileMain back={true} />}</div>;
};

export default WorkerMenuMain;
