import { FC, useState } from 'react';
import { useAppDispatch } from '../../../../../useHook/useRedux';
import { Category, createCategory } from '../../../../../store/slices/menuSlice';
import UseModal from '../../../../../useHook/useModal/UseModal';
import UseForm from '../../../../../useHook/useForm/useForm';
import ButtonAdmin from '../../ButtonAdmin';
interface modalCreateType {
    visible: boolean;
    item: Category | null;
}
const CategoryCreate: FC = () => {
    const [modalCreate, setModalCreate] = useState<modalCreateType>({
        visible: false,
        item: null,
    });
    const dispatch = useAppDispatch();

    const closeModal = () =>
        setModalCreate(() => {
            return {
                item: null,
                visible: false,
            };
        });

    const onModal = () => {
        setModalCreate(() => {
            return {
                item: null,
                visible: true,
            };
        });
    };
    const submitCreate = async (formData: FormData) => {
        const req = await dispatch(createCategory(formData));
        if (req.payload === 200) closeModal();
    };
    const initialNames = {
        name: 'Названиe',
    };

    const initialValues = {
        name: '',
        img: [],
    };
    return (
        <>
            <ButtonAdmin text='Create Category' handle={onModal} />
            <UseModal
                visible={modalCreate.visible}
                title='Create Category'
                content={
                    <UseForm
                        initialNames={initialNames}
                        initialValues={initialValues}
                        subminFunc={submitCreate}
                        handleForm={closeModal}
                        img={true}
                    />
                }
                onClose={closeModal}
            />
        </>
    );
};

export default CategoryCreate;
