import { FC, useState, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import UseModal from '../../../../../useHook/useModal/UseModal';
import { getStock, addStockToAddition, Stock } from '../../../../../store/slices/menuSlice';
import { stockColumn } from '../columnsMenu';
import ButtonAdmin from '../../ButtonAdmin';
interface AdditionAddStockProps {
    additionStock?: Stock[];
}
const AdditionAddStock: FC<AdditionAddStockProps> = ({ additionStock }) => {
    const [modal, setModal] = useState(false);
    const dispatch = useAppDispatch();
    const stock = useAppSelector((state) => state.menu.stock);

    const additionStockIds = additionStock?.map((item) => item.id);
    const filteredStock = stock.filter((item) => !additionStockIds?.includes(item.id));

    useEffect(() => {
        dispatch(getStock());
    }, []);
    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };

    return (
        <>
            <ButtonAdmin text='Add Stock' handle={onModal} />
            <UseModal
                visible={modal}
                title={`Add Stock`}
                content={
                    <>
                        <UseTable
                            columns={stockColumn}
                            data={filteredStock}
                            link={false}
                            rel={true}
                            addRel={true}
                            addFunc={addStockToAddition}
                        />
                    </>
                }
                onClose={closeModal}
            />
        </>
    );
};

export default AdditionAddStock;
