import { FC, useState } from 'react';
import flag_uk from './../../style/image/flag_uk.png';
import flag_en from './../../style/image/flag_en.png';
import flag_ru from './../../style/image/flag_ru.png';
interface Item {
    id: number;
    label: string;
    img: any;
    target: string;
}

const data: Item[] = [
    { id: 0, label: 'Український', img: flag_uk, target: 'ua' },
    { id: 1, label: 'English', img: flag_en, target: 'en' },
    { id: 2, label: '-', img: flag_ru, target: 'ru' },
];
interface LangDropIconProps {
    toggleDropdown: () => void;
    selectedItem: string;
    isOpen: boolean;
    handleLanguageChange: (arg: string) => void;
}
const LangDropIcon: FC<LangDropIconProps> = ({ toggleDropdown, selectedItem, isOpen, handleLanguageChange }) => {
    return (
        <div className='lang_dropdown'>
            <div className='lang_dropdown-header' onClick={toggleDropdown}>
                <img src={data.find((item) => item.target === selectedItem)?.img} alt='' />
            </div>
            <div className={`lang_dropdown-body ${isOpen && 'open'}`}>
                {data.map((item) => (
                    <div
                        key={item.id}
                        className='lang_dropdown-item'
                        onClick={() => {
                            handleLanguageChange(item.target);
                            toggleDropdown();
                        }}
                        id={item.id.toString()}>
                        <span>{item.label}</span>
                        <img src={item.img} alt='' />
                    </div>
                ))}
            </div>
        </div>
    );
};

export default LangDropIcon;
