import { Formik, Field, Form, ErrorMessage } from 'formik';
import { useState } from 'react';
// interface UseFormProps {
//   initialValues?: any;
//   initialNames: { [key: string]: string };
//   validSchema?: any;
//   subminFunc: (formData: FormData) => void;
//   handleForm?: () => void;
//   img: boolean;
// }

const UseForm = ({ initialValues, initialNames, validSchema = '', subminFunc, handleForm, img = false }) => {
    const [fileName, setfileName] = useState();

    const onChangePhoto = (event, setFieldValue) => {
        const file = event.target.files[0];
        setFieldValue('img', file);

        setfileName(file.name);
    };

    return (
        <Formik
            initialValues={initialValues}
            validationSchema={validSchema}
            onSubmit={(body) => {
                const formData = new FormData();
                Object.entries(body).map(([key, value]) => {
                    return formData.append(`${key}`, value);
                });

                subminFunc(formData);
            }}
        >
            {({ setFieldValue, errors, touched }) => (
                <Form>
                    <div className='form_put'>
                        {Object.entries(initialNames).map(([key, value]) => (
                            <div className='put_input_wrapper' key={key} style={{ display: 'flex' }}>
                                <span>{value}</span>
                                <Field className='pass' type='text' placeholder={value} name={key} />
                                <ErrorMessage name={key} className='error password' component='div' />
                            </div>
                        ))}
                        {img && (
                            <label
                                className='input-img'
                                style={
                                    errors.photo && touched.photo
                                        ? {
                                              border: '2px solid red',
                                              borderRadius: '6px 0px 0px 6px',
                                          }
                                        : {}
                                }
                                htmlFor='img'
                            >
                                <div className='input-img__left'>
                                    <span>Upload</span>
                                </div>
                                <span className='input-img__right'>{fileName ? fileName : 'Upload your img'}</span>
                                <input
                                    className='form-input__none'
                                    type='file'
                                    id='img'
                                    name='img'
                                    onChange={(event) => onChangePhoto(event, setFieldValue)}
                                />
                            </label>
                        )}
                    </div>

                    <div className='modal_footer'>
                        <button type='button' onClick={handleForm}>
                            Exit
                        </button>
                        <button type='submit'>Confirm</button>
                    </div>
                </Form>
            )}
        </Formik>
    );
};

export default UseForm;
