import { FC } from 'react';
import { Twirl as HamburgerIcon } from 'hamburger-react';
import { NavLink } from 'react-router-dom';
import { burgetMenuPrivate } from '../../constatns/translateDefaultUser';
import './hamburger.css';
import { useLanguage } from '../../utils/LanguageContext';
interface HamburgerProps {
    isOpen: boolean;
    closeModal: () => void;
    lang: string;
}
const Hamburger: FC<HamburgerProps> = ({ isOpen, closeModal, lang }) => {
    const { languageSlug } = useLanguage();

    return (
        <>
            {isOpen && (
                <div className='hamburger' onClick={closeModal}>
                    <div className='hamburger_content ' onClick={(e) => e.stopPropagation()}>
                        <HamburgerIcon toggled={isOpen} toggle={closeModal} />
                        <div className='hamburger_nav'>
                            <NavLink to={`/${languageSlug}/signin`} className='NavLink'>
                                {burgetMenuPrivate[languageSlug]}
                            </NavLink>
                        </div>
                    </div>
                </div>
            )}
        </>
    );
};

export default Hamburger;
