import { FC, useState } from 'react';
import { useAppSelector, useAppDispatch } from '../../../../../../useHook/useRedux';
import { Basket, delItemBasket } from '../../../../../../store/slices/orderSlice';
import WorkerMobileModalForProduct from '../modalForWorkerMobileCard/WorkerMobileModalForProduct';
import cancel_icon from '../../../../../../style/image/cancel.png';
import cardImg from '../../../../../../style/image/card.png';
import additCard from '../../../../../../style/image/pngwing.png';

const WorkerMobileBasketCard: FC<Basket> = ({ product, count, price, additions, keyInOrder }) => {
    const [isOpenChange, setIsOpenChange] = useState(false);
    const products = useAppSelector((state) => state.menu.product).filter(({ name }) => name === product.name);
    const dispatch = useAppDispatch();
    const openModalChange = () => {
        setIsOpenChange(true);
    };

    const closeModalChange = () => {
        setIsOpenChange(false);
    };
    const handleCancel = () => dispatch(delItemBasket(keyInOrder));
    return (
        <div className='basket_card'>
            <img className='basket_card_cancelIcon' onClick={handleCancel} src={cancel_icon} alt='cancel icon' />
            <div className='basket_card_product'>
                <img src={product.img_path} alt='product' />
                <div className='basket_card_text'>
                    <span className='basket_card_name'>{product.name}</span>
                    <span className='basket_card_description'>{product.description}</span>
                    <div>
                        <span className='basket_card_amount'>{product.amount}</span>
                        <span className='basket_card_count'>Количество: {count}</span>
                    </div>
                </div>
            </div>

            {additions.length > 0 && <span className='additions_title'>Добавки:</span>}
            <div className='basket_card_additions_cards'>
                {additions?.map((addition) => (
                    <div key={addition.name} className='basket_card_additions_card '>
                        <img src={additCard} alt='' />
                        <span className='basket_card_additions_card_title'>{addition.name}</span>
                    </div>
                ))}
            </div>

            <div className='basket_card_bottom'>
                <button onClick={openModalChange}>Изменить</button>
                <span className='price'>{price} грн.</span>
            </div>
            <WorkerMobileModalForProduct
                isOpen={isOpenChange}
                closeModal={closeModalChange}
                product={products}
                order={{ product, count, price, additions, keyInOrder }}
            />
        </div>
    );
};

export default WorkerMobileBasketCard;
