import { FC } from 'react';
import { NavLink } from 'react-router-dom';

import './workerCard.css';
import { useLanguage } from '../../../utils/LanguageContext';
interface WorkerCardProps {
    img_src: string;
    title: string;
    to: string;
}

const WorkerCard: FC<WorkerCardProps> = ({ img_src, title, to }) => {
    const { languageSlug } = useLanguage();
    return (
        <NavLink to={`/${languageSlug}${to}`} className='worker-main_card'>
            <div className='worker-main_card_img-container'>
                <img src={img_src} alt={title} />
            </div>
            <span className='worker-main_card_text'>{title}</span>
        </NavLink>
    );
};

export default WorkerCard;
