import { useState } from 'react';
import UseModal from '../../useModal/UseModal';
import { useParams } from 'react-router-dom';
import { useAppDispatch } from '../../useRedux';
import { useLanguage } from '../../../utils/LanguageContext';
import imgDelete from '../../../style/image/delete.png';
import { useNavigate } from 'react-router-dom';
const DeleteItemTable = ({ item, delFunc, rel, order, product_id }) => {
    const { id } = useParams();
    const navigate = useNavigate();
    const { languageSlug } = useLanguage();
    const [modal, setModal] = useState(false);
    const dispatch = useAppDispatch();
    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };

    const handleDel = async () => {
        switch (order) {
            case 'product':
                const reqProduct = await dispatch(delFunc({ id, product_id: item.id }));

                if (reqProduct.payload.status === 200) {
                    closeModal();
                    if (!reqProduct.payload.order) navigate(`/${languageSlug}/order`);
                }
                return;
            case 'addition':
                console.log(id, product_id, item.id);
                const reqAddition = await dispatch(delFunc({ id: id, product_id, addition_id: item.id }));
                if (reqAddition.payload === 200) {
                    closeModal();
                }
                return;
        }
        if (rel) {
            const req = await dispatch(delFunc({ id: id, name: item.name }));
            if (req.payload === 200) {
                closeModal();
            }
        } else {
            const req = await dispatch(delFunc(item.id));
            if (req.payload === 200) {
                closeModal();
            }
        }
    };

    return (
        <>
            <img className='table-icon' src={imgDelete} alt='delete' onClick={onModal} style={{ cursor: 'pointer' }} />
            <UseModal
                visible={modal}
                title={`Delete ${item.name}`}
                content={
                    <>
                        <span>{item.name}</span>
                    </>
                }
                onClose={closeModal}
                footer={
                    <>
                        <button type='button' onClick={closeModal}>
                            Exit
                        </button>
                        <button onClick={handleDel}>Confirm</button>
                    </>
                }
            />
        </>
    );
};

export default DeleteItemTable;
