import { FC } from 'react';
import './asideBasketDekstop.css';
import { useAppSelector, useAppDispatch } from '../../useHook/useRedux';
import { NavLink } from 'react-router-dom';
import AsideBasketDekstopCard from './AsideBasketDekstopCard';
import { useLanguage } from '../../utils/LanguageContext';
import { BasketTr, PriceTr } from '../../constatns/translateDefaultUser';
import { spawn } from 'child_process';
const AsideBasketDekstop: FC = () => {
    const basket = useAppSelector((state) => state.order.basket);
    const totalPrice = basket.reduce(
        (total, item) => total + item.price + item.additions.reduce((total, item) => total + item.price, 0),
        0,
    );
    const { languageSlug } = useLanguage();
    return (
        <div className='mainMenuClientDekstop_asideBasket'>
            <span className='mainMenuClientDekstop_asideBasket_title'>{BasketTr.yourOrder[languageSlug]}</span>
            <div className='mainMenuClientDekstop_asideBasket_cards'>
                {basket.length > 0 ? (
                    basket.map((basket) => (
                        <AsideBasketDekstopCard
                            key={basket.keyInOrder}
                            keyInOrder={basket.keyInOrder}
                            product={basket.product}
                            additions={basket.additions}
                            price={basket.price}
                            count={basket.count}
                        />
                    ))
                ) : (
                    <span>Net Sakasov</span>
                )}
            </div>
            <div className='mainMenuClientDekstop_asideBasket_price'>
                <span>{PriceTr.sum[languageSlug]}</span>
                <span>
                    {totalPrice} {PriceTr.currency[languageSlug]}
                </span>
            </div>
            {basket.length > 0 ? (
                <NavLink to={`/${languageSlug}/basket`} className='mainMenuClientDekstop_asideBasket_button'>
                    {BasketTr.placOrder[languageSlug]}
                </NavLink>
            ) : (
                <span className='mainMenuClientDekstop_asideBasket_button'>{BasketTr.placOrder[languageSlug]}</span>
            )}
        </div>
    );
};

export default AsideBasketDekstop;
