import React from 'react';
import FilterInputs from './FilterInputs';

const HeaderTable = ({ columns, addRel, styleHead }) => (
    <>
        <tr className='table_thead_tr'>
            {columns.map((column) => {
                if (column.accessor === 'num') {
                    return (
                        <th key={column.header}>
                            <div>№</div>
                        </th>
                    );
                }
                if (column.accessor === 'del' && addRel) return;
                if (column.accessor === 'del') {
                    return (
                        <th key={column.header}>
                            <div>Del</div>
                        </th>
                    );
                }
                return (
                    <th key={column.accessor}>
                        <div>{column.header}</div>
                    </th>
                );
            })}
            {addRel && (
                <th>
                    <div>Add</div>
                </th>
            )}
        </tr>
        {!styleHead && <tr className='table_margin'></tr>}
    </>
);

export default HeaderTable;
