import { FC, useEffect, useState } from 'react';
import { getPermission, Permission, addPermissionToUser } from '../../../../../store/slices/userAdminSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import UseModal from '../../../../../useHook/useModal/UseModal';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { permissionColumn } from '../columnUsers';
import ButtonAdmin from '../../ButtonAdmin';

interface AddPermissionToAdminProps {
    permissionUser?: Permission[];
}

const AddPermissionToAdmin: FC<AddPermissionToAdminProps> = ({ permissionUser }) => {
    const [modal, setModal] = useState(false);
    const dispatch = useAppDispatch();
    const permissions = useAppSelector((state) => state.adminUser.permission);

    const permissionUserIds = permissionUser?.map((item) => item.id);
    const filteredPermission = permissions.filter((item) => !permissionUserIds?.includes(item.id));
    useEffect(() => {
        dispatch(getPermission());
    }, []);
    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };

    return (
        <>
            <ButtonAdmin text='Add permissions' handle={onModal} />
            <UseModal
                visible={modal}
                title={`Add permissions`}
                content={
                    <>
                        <UseTable
                            columns={permissionColumn}
                            data={filteredPermission}
                            link={false}
                            rel={true}
                            addRel={true}
                            addFunc={addPermissionToUser}
                        />
                    </>
                }
                onClose={closeModal}
            />
        </>
    );
};

export default AddPermissionToAdmin;
