import React, { FC } from 'react';
import { useAppSelector } from '../../../../../useHook/useRedux';
import { useParams } from 'react-router-dom';
import CategoryPut from './CategoryPut';
import { categoryColumn } from '../columnsMenu';
const CategoryDetails: FC = () => {
    const { id } = useParams();
    const category = useAppSelector((state) => state.menu.category).filter(
        (addition) => addition.id == parseInt(id ? id : ''),
    )[0];
    return (
        <div>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Category</span>
                <CategoryPut id={category.id} name={category.name} />
            </div>
            <div className='adminDetails_item_wrapper'>
                {categoryColumn.map((i) => {
                    if (!category[i.accessor]) return;
                    return (
                        <div className='adminDetails_item'>
                            <span>{i.header}: </span>
                            <span> {category[i.accessor]}</span>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default CategoryDetails;
