import React, { FC } from 'react';
import { useAppSelector } from '../../../../../useHook/useRedux';
import { useParams } from 'react-router-dom';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import AdditionPut from './AdditionPut';
import AdditionAddStock from './AdditionAddStock';
import { removeStockFromAddition } from '../../../../../store/slices/menuSlice';
import { stockColumn, additionColumn } from '../columnsMenu';
const AdditionDetails: FC = () => {
    const { id } = useParams();
    const addition = useAppSelector((state) => state.menu.addition).filter(
        (addition) => addition.id === parseInt(id ? id : ''),
    )[0];

    return (
        <div>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Addition</span>
                <AdditionPut id={addition.id} name={addition.name} price={addition.price} />
            </div>

            <div className='adminDetails_item_wrapper'>
                {additionColumn.map((i) => {
                    if (!addition[i.accessor]) return;
                    return (
                        <div className='adminDetails_item'>
                            <span>{i.header}: </span>
                            <span> {addition[i.accessor]}</span>
                        </div>
                    );
                })}
            </div>

            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Stock:</span>
                <AdditionAddStock additionStock={addition?.Stocks} />
            </div>

            {addition?.Stocks && (
                <UseTable
                    columns={stockColumn}
                    data={addition.Stocks}
                    link={false}
                    delFunc={removeStockFromAddition}
                    rel={true}
                />
            )}
        </div>
    );
};

export default AdditionDetails;
