import { FC, useEffect } from 'react';
import './modalForWorkerMobileProduct.css';
import WorkerMobileModalContetnProduct from './WorkerMobileModalContetnProduct';
import { Product } from '../../../../../../store/slices/menuSlice';
import { Basket } from '../../../../../../store/slices/orderSlice';
interface ModalForProductProps {
    isOpen: boolean;
    closeModal: () => void;
    product: Product[];
    order?: Basket;
}
const WorkerMobileModalForProduct: FC<ModalForProductProps> = ({ isOpen, closeModal, product, order }) => {
    useEffect(() => {
        if (isOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'auto';
        }
        return () => {
            document.body.style.overflow = 'auto';
        };
    }, [isOpen]);
    return (
        <>
            {isOpen && (
                <div className='modal_worker' onClick={closeModal}>
                    <div className='modal_worker_content' onClick={(e) => e.stopPropagation()}>
                        <WorkerMobileModalContetnProduct closeModal={closeModal} product={product} order={order} />
                    </div>
                </div>
            )}
        </>
    );
};

export default WorkerMobileModalForProduct;
