import { FC } from 'react';
import { useAppSelector } from '../../../../useHook/useRedux';
import { useParams } from 'react-router-dom';
import OrderPut from './OrderPut';
import { orderColumn } from './columnsOrder';

const OrderDetails: FC = () => {
    const { id } = useParams();
    const order = useAppSelector((state) => state.order.order).filter(
        (order) => order.id === parseInt(id ? id : ''),
    )[0];

    return (
        <div>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Order</span>
                <OrderPut id={order.id} status={order.status} payment={order.payment} comment={order.comment} />
            </div>
            <div className='adminDetails_item_wrapper'>
                {orderColumn.map((i) => {
                    if (!order[i.accessor]) return;
                    return (
                        <div key={i.header} className='adminDetails_item'>
                            <span>{i.header}: </span>
                            <span> {order[i.accessor]}</span>
                        </div>
                    );
                })}
            </div>

            {order && (
                <div className='orderWorkerDetails_modal' style={{ width: '95%', margin: '0 auto' }}>
                    {order.Products.length > 0 && (
                        <div className='orderWorkerDetails_modal_wrapper'>
                            <div className='orderWorkerDetails_modal_main'>
                                <div className='orderWorkerDetails_modal_main_head'>
                                    <span>№</span>
                                    <span>Product</span>
                                    <span>Q-ty</span>
                                    <span>Amount</span>
                                    <span>Total price</span>
                                </div>
                                <div className='orderWorkerDetails_modal_main_body'>
                                    {order.Products.map((prod, index) => {
                                        return (
                                            <div className='orderWorkerDetails_modal_main_body_card'>
                                                <div className='orderWorkerDetails_modal_main_body_card_prod'>
                                                    <span>{index + 1}</span>
                                                    <span>{prod.name}</span>
                                                    <span>{prod.ProoductsOrder.count}</span>
                                                    <span>{prod.amount}</span>
                                                    <span>
                                                        {(prod.price +
                                                            prod.Additions.reduce(
                                                                (start, item) => start + item.price,
                                                                0,
                                                            )) *
                                                            prod.ProoductsOrder.count}
                                                    </span>
                                                </div>
                                                {prod.Additions.length > 0 && (
                                                    <div className='orderWorkerDetails_modal_main_body_card_addit'>
                                                        <span>Additions:</span>

                                                        {prod.Additions.map((add, index) => (
                                                            <span>
                                                                {add.name} {prod.Additions.length > index + 1 && ','}
                                                            </span>
                                                        ))}
                                                    </div>
                                                )}
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        </div>
                    )}

                    {order.comment && (
                        <div className='orderWorkerDetails_modal_comment'>
                            <span>Comment:</span>
                            <span>{order.comment}</span>
                        </div>
                    )}
                </div>
            )}
        </div>
    );
};

export default OrderDetails;
