import { FC, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { getAddition, delAddition } from '../../../../../store/slices/menuSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import AdditionCreate from './AdditionCreate';
import { additionColumn } from '../columnsMenu';
import './../adminMenu.css';
const AdditionMain: FC = () => {
    const dispatch = useAppDispatch();
    const additionData = useAppSelector((state) => state.menu.addition);
    useEffect(() => {
        dispatch(getAddition());
    }, []);

    return (
        <>
            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Addition</span>
                <AdditionCreate />
            </div>

            {additionData && (
                <UseTable columns={additionColumn} link={true} data={additionData} delFunc={delAddition} />
            )}
        </>
    );
};

export default AdditionMain;
