import { FC, useState } from 'react';
import { useAppDispatch } from '../../../../useHook/useRedux';
import { putOrder, Order } from '../../../../store/slices/orderSlice';
import UseModal from '../../../../useHook/useModal/UseModal';
import UseForm from '../../../../useHook/useForm/useForm';
import ButtonAdmin from '../ButtonAdmin';

interface modalCreateType {
    visible: boolean;
    item: Order | null;
}
interface OrderPutProps {
    id: number;
    status: string;
    payment: string;
    comment: string;
}

const OrderPut: FC<OrderPutProps> = ({ id, status, payment, comment }) => {
    const [modalCreate, setModalCreate] = useState<modalCreateType>({
        visible: false,
        item: null,
    });
    const dispatch = useAppDispatch();

    const closeModal = () =>
        setModalCreate(() => {
            return {
                item: null,
                visible: false,
            };
        });

    const onModal = () => {
        setModalCreate(() => {
            return {
                item: null,
                visible: true,
            };
        });
    };
    const submitCreate = async (formData: FormData) => {
        if (id) {
            const req = await dispatch(putOrder({ formData, id }));
            if (req.payload === 200) closeModal();
        }
    };
    const initialNames = {
        status: 'Status',
        payment: 'Payment',
        comment: 'Comment',
    };
    const initialValues = {
        status: status,
        payment: payment,
        comment: comment,
    };
    return (
        <>
            <ButtonAdmin text='PUT Order' handle={onModal} />

            <UseModal
                visible={modalCreate.visible}
                title='PUT Order'
                content={
                    <UseForm
                        initialNames={initialNames}
                        initialValues={initialValues}
                        subminFunc={submitCreate}
                        handleForm={closeModal}
                        // img={true}
                    />
                }
                onClose={closeModal}
            />
        </>
    );
};

export default OrderPut;
