import { FC } from 'react';
import { useParams } from 'react-router-dom';
import { removePermissionFromAdmin } from '../../../../../store/slices/userAdminSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import { useAppSelector } from '../../../../../useHook/useRedux';
import AddPermissionToAdmin from './AddPermissionToAdmin';
import { permissionColumn, userDeteils } from '../columnUsers';

const UserAdminDetails: FC = () => {
    const { id } = useParams();
    const admin = useAppSelector((state) => state.adminUser.admin).filter(
        (admin) => admin.id === parseInt(id ? id : ''),
    )[0];

    return (
        <div>
            <div className='adminDetails_item_wrapper'>
                {userDeteils.map((i) => {
                    if (!admin[i.accessor]) return;
                    return (
                        <div className='adminDetails_item'>
                            <span>{i.header}: </span>
                            <span> {admin[i.accessor]}</span>
                        </div>
                    );
                })}
            </div>

            <div className='menu_main_top'>
                <span className='menu_main_top_title'>Permission:</span>
                <AddPermissionToAdmin permissionUser={admin.Permissions} />
            </div>

            {admin?.Permissions && (
                <>
                    <UseTable
                        columns={permissionColumn}
                        data={admin.Permissions}
                        link={false}
                        delFunc={removePermissionFromAdmin}
                        rel={true}
                    />
                </>
            )}
        </div>
    );
};

export default UserAdminDetails;
