import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import { Order } from './orderSlice';
import $api from '../../axios';

export const createAdmin = createAsyncThunk<number, FormData>(
    'menu/createCategory',
    async (formData, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.post('/user/admin', formData);

            dispatch(getAdmin());
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);
export const createWorker = createAsyncThunk<number, FormData>(
    'menu/createCategory',
    async (formData, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.post('/user/admin/worker', formData);

            dispatch(getWorker());
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);
export const getAdmin = createAsyncThunk<number>('userAdmin/getAdmin', async (_, { rejectWithValue, dispatch }) => {
    try {
        const response = await $api.get('/user/admin');
        dispatch(setAdmin(response.data.data));
        return response.status;
    } catch (error) {
        return rejectWithValue((error as Error).message);
    }
});

