export const addPermissionToUser = createAsyncThunk<number, { id: string; name: string }>(
    'menu/addPermissionToUser',
    async ({ id, name }, { rejectWithValue, dispatch }) => {
        try {
            console.log(true);
            const body = new FormData();
            body.append('user_id', id);
            body.append('permission_name', name);
            const response = await $api.post(`/permission`, body);
            switch (response.data.user_type) {
                case 'admin':
                    dispatch(getAdmin());
                    break;

                case 'worker':
                    dispatch(getWorker());
                    break;
                case 'client':
                    dispatch(getClient());
                    break;
            }
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);

export const removePermissionFromAdmin = createAsyncThunk<number, { id: number; name: string }>(
    'menu/removePermissionFromAdmin',
    async ({ id, name }, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.delete(`/permission?user_id=${id}&permission_name=${name}`);
            switch (response.data.user_type) {
                case 'admin':
                    dispatch(getAdmin());
                    break;

                case 'worker':
                    dispatch(getWorker());
                    break;
                case 'client':
                    dispatch(getClient());
                    break;
            }
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);

export const delUser = createAsyncThunk<number, number>(
    'userAdmin/delUser',
    async (id, { rejectWithValue, dispatch }) => {
        try {
            const response = await $api.delete(`/user/admin/${id}`);
            switch (response.data.user_type) {
                case 'admin':
                    dispatch(getAdmin());
                    break;

                case 'worker':
                    dispatch(getWorker());
                    break;
                case 'client':
                    dispatch(getClient());
                    break;
            }
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);

export interface User {
    id: number;
    name: string;
    type: string;
    login: string;
    Permissions: Permission[];
    Roles: Role[];
    Orders: Order[];
    img_path: string;
    createdAt: string;
    updatedAt: string;
    [key: string]: any;
}
export interface Permission {
    id: number;
    name: string;
    createdAt: string;
    updatedAt: string;
    [key: string]: any;
}

export interface Role {
    id: number;
    name: string;
    createdAt: string;
    updatedAt: string;
    [key: string]: any;
}

interface State {
    admin: User[];
    worker: User[];
    client: User[];
    permission: Permission[];
    loading: boolean;
    error: string | null;
}

const initialState: State = {
    admin: [],
    worker: [],
    client: [],
    permission: [],
    loading: false,
    error: null,
};

const userAdminSlice = createSlice({
    name: 'userAdmin',
    initialState,
    reducers: {
        setClient(state, action: PayloadAction<User[]>) {
            state.client = action.payload;
        },
        setAdmin(state, action: PayloadAction<User[]>) {
            state.admin = action.payload;
        },
        setWorker(state, action: PayloadAction<User[]>) {
            state.worker = action.payload;
        },
        setPermission(state, action: PayloadAction<Permission[]>) {
            state.permission = action.payload;
        },
    },
    extraReducers: (builder) => {
        // builder
        //     .addCase(signIn.pending, (state) => {
        //         state.loading = true;
        //         state.error = null;
        //     })
        //     .addCase(signIn.fulfilled, (state) => {
        //         state.loading = false;
        //         state.error = null;
        //     })
        //     .addCase(signIn.rejected, (state, action) => {
        //         state.loading = false;
        //         state.error = action.payload as string;
        //     });
    },
});

export const { setAdmin, setClient, setWorker, setPermission } = userAdminSlice.actions;

export default userAdminSlice.reducer;
