import React, { FC, useEffect } from 'react';
import './modalForProduct.css';
import ModalMobileContetnProduct from './ModalMobileContetnProduct';
import { Product } from '../../../store/slices/menuSlice';
import { Basket } from '../../../store/slices/orderSlice';
interface ModalMobileForProductProps {
    isOpen: boolean;
    closeModal: () => void;
    product: Product[];
    order?: Basket;
    lang: string;
}
const ModalMobileForProduct: FC<ModalMobileForProductProps> = ({ isOpen, closeModal, product, order, lang }) => {
    useEffect(() => {
        if (isOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'auto';
        }
        return () => {
            document.body.style.overflow = 'auto';
        };
    }, [isOpen]);
    return (
        <>
            {isOpen && (
                <div className='modal_client' onClick={closeModal}>
                    <div className='modal-content_client' onClick={(e) => e.stopPropagation()}>
                        <ModalMobileContetnProduct
                            lang={lang}
                            closeModal={closeModal}
                            product={product}
                            order={order}
                        />
                    </div>
                </div>
            )}
        </>
    );
};

export default ModalMobileForProduct;
