import { FC, useState } from 'react';
import { useAppDispatch } from '../../../../../useHook/useRedux';
import { Addition, putAddition } from '../../../../../store/slices/menuSlice';
import UseModal from '../../../../../useHook/useModal/UseModal';
import UseForm from '../../../../../useHook/useForm/useForm';
import ButtonAdmin from '../../ButtonAdmin';
interface modalCreateType {
    visible: boolean;
    item: Addition | null;
}

const AdditionPut: FC<Addition> = ({ id, name, price }) => {
    const [modalCreate, setModalCreate] = useState<modalCreateType>({
        visible: false,
        item: null,
    });
    const dispatch = useAppDispatch();

    const closeModal = () =>
        setModalCreate(() => {
            return {
                item: null,
                visible: false,
            };
        });

    const onModal = () => {
        setModalCreate(() => {
            return {
                item: null,
                visible: true,
            };
        });
    };
    const submitCreate = async (formData: FormData) => {
        if (id) {
            const req = await dispatch(putAddition({ formData, id }));
            if (req.payload === 200) closeModal();
        }
    };
    const initialNames = {
        name: 'Названиe',
        price: 'Цена',
    };
    const initialValues = {
        name: name,
        price: price,
    };
    return (
        <>
            <ButtonAdmin handle={onModal} text='PUT Addition' />
            {/* <button onClick={onModal}>PUT Addition</button> */}
            <UseModal
                visible={modalCreate.visible}
                title='PUT Addition'
                content={
                    <UseForm
                        initialNames={initialNames}
                        initialValues={initialValues}
                        subminFunc={submitCreate}
                        handleForm={closeModal}
                        img={true}
                    />
                }
                onClose={closeModal}
            />
        </>
    );
};

export default AdditionPut;
