import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';

import $api from '../../axios';
interface SignInFormValues {
    login: string;
    password: string;
}

interface UserPayload {
    status: boolean;
    acsses: string;
    data: {
        id: number;
        name: string;
        login: string;
        type: string;
    };
}

export const signIn = createAsyncThunk<number, SignInFormValues>(
    'user/signIn',
    async (formData, { rejectWithValue, dispatch }) => {
        try {
            const body = new FormData();
            body.append('login', formData.login);
            body.append('password', formData.password);
            const response = await $api.post('/signin', body);
            localStorage.setItem('token', response.data.access);
            dispatch(setUser(response.data));
            return response.status;
        } catch (error) {
            return rejectWithValue((error as Error).message);
        }
    },
);

interface State {
    access: string | null;
    lang: string;
    data: {
        id: number;
        name: string;
        login: string;
        type: string;
    } | null;
    loading: boolean;
    error: string | null;
}

const initialState: State = {
    access: null,
    lang: 'ua',
    data: null,
    loading: false,
    error: null,
};

const userSlice = createSlice({
    name: 'user',
    initialState,
    reducers: {
        setUser(state, action: PayloadAction<UserPayload>) {
            state.access = action.payload.acsses;
            state.data = action.payload.data;
        },
        setLang(state, action: PayloadAction<string>) {
            state.lang = action.payload;
        },
        logout(state) {
            console.log('logout');
            state.access = null;
            state.data = null;
        },
    },
    extraReducers: (builder) => {
        builder
            .addCase(signIn.pending, (state) => {
                state.loading = true;
                state.error = null;
            })
            .addCase(signIn.fulfilled, (state) => {
                state.loading = false;
                state.error = null;
            })
            .addCase(signIn.rejected, (state, action) => {
                state.loading = false;
                state.error = action.payload as string;
            });
    },
});

export const { setUser, setLang, logout } = userSlice.actions;

export default userSlice.reducer;
