import { FC, useState } from 'react';
import WorkerMobileModalForProduct from './modalForWorkerMobileCard/WorkerMobileModalForProduct';
import { Product } from '../../../../../store/slices/menuSlice';
interface MenuCardProps {
    product: Product[];
}
const WorkerMobileProductCard: FC<MenuCardProps> = ({ product }) => {
    const [isOpen, setIsOpen] = useState(false);

    const openModal = () => {
        setIsOpen(true);
    };

    const closeModal = () => {
        setIsOpen(false);
    };
    return (
        <>
            <div className='menuCard'>
                <img className='menuCard_img' src={product[0].img_path} alt='Menu card' />
                <div className='menuCard_text'>
                    <div>
                        <span>{product[0].name}</span>
                        <span>{product[0].description}</span>
                    </div>
                    <button onClick={openModal} className='menuCard_button'>
                        Выбрать
                    </button>
                </div>
            </div>

            <WorkerMobileModalForProduct isOpen={isOpen} closeModal={closeModal} product={product} />
        </>
    );
};

export default WorkerMobileProductCard;
