import { useState } from 'react';
import UseModal from '../../useModal/UseModal';
import { useParams } from 'react-router-dom';
import { useAppDispatch } from '../../useRedux';
import imgAdd from '../../../style/image/add.jpeg';

const AddRelToItem = ({ item, addFunc }) => {
    const { id } = useParams();
    const [modal, setModal] = useState(false);
    const dispatch = useAppDispatch();
    const onModal = () => {
        setModal(true);
    };
    const closeModal = () => {
        setModal(false);
    };

    const handleAdd = async () => {
        const req = await dispatch(addFunc({ id: id, name: item.name }));
        if (req.payload === 200) {
            closeModal();
        }
    };

    return (
        <>
            <span style={{ fontSize: '25px', width: '50px', color: '#F2542D', cursor: 'pointer' }} onClick={onModal}>
                +
            </span>
            <UseModal
                visible={modal}
                title={`Add ${item.name}`}
                content={
                    <>
                        <span>{item.name}</span>
                    </>
                }
                onClose={closeModal}
                footer={
                    <>
                        <button type='button' onClick={closeModal}>
                            Exit
                        </button>
                        <button onClick={handleAdd}>Confirm</button>
                    </>
                }
            />
        </>
    );
};

export default AddRelToItem;
