import React, { FC, useState, useEffect } from 'react';
import { Product, Addition } from '../../../../../../store/slices/menuSlice';
import { Basket } from '../../../../../../store/slices/orderSlice';
import { useAppDispatch } from '../../../../../../useHook/useRedux';
import { addOrderData, updateOrderData } from '../../../../../../store/slices/orderSlice';
import addition_img from '../../../../../../style/image/pngwing.png';

interface ModalContetnProductProps {
    closeModal: () => void;
    product: Product[];
    order?: Basket;
}
const WorkerMobileModalContetnProduct: FC<ModalContetnProductProps> = ({ closeModal, product, order }) => {
    const dispatch = useAppDispatch();
    const [productAmount, setProductAmount] = useState<number>(product[0].id);
    const [additionCheck, setAdditionCheck] = useState<number[]>([]);
    const [count, setCount] = useState(1);
    // selected produtc
    const selectedProduct = product.find((item) => item.id === productAmount) as Product;
    // price
    const price = selectedProduct.price * count;

    useEffect(() => {
        if (order) {
            setProductAmount(order.product.id);
            const orderAdditions = order.additions.map(({ id }) => id);
            setAdditionCheck(orderAdditions);
            setCount(order.count);
        }
    }, []);

    // handle
    const handleAmount = (amount: number) => {
        setProductAmount(amount);
    };
    const handleCounter = (operation: string) => {
        if (operation === 'increment') {
            setCount(count + 1);
        } else if (operation === 'decrement' && count > 1) {
            setCount(count - 1);
        }
    };
    const handleAddition = (additionId: number) => {
        if (additionCheck.includes(additionId)) {
            setAdditionCheck(additionCheck.filter((id) => id !== additionId));
        } else {
            setAdditionCheck([...additionCheck, additionId]);
        }
    };
    // order
    const handleAddToCart = () => {
        const orderData = {
            product: selectedProduct,
            count: count,
            price: price,
            keyInOrder: order ? order.keyInOrder : Math.random(),
            additions: additionCheck
                .map((id) => selectedProduct.Additions?.find((addition) => addition.id === id))
                .filter((addition) => addition !== undefined) as Addition[],
        };

        order ? dispatch(updateOrderData(orderData)) : dispatch(addOrderData(orderData));
        closeModal();
    };

    return (
        <>
            <div className='product_wrapper'>
                <div className='product'>
                    <span className='product_name'>{product[0].name}</span>

                    <div className='product_counter'>
                        <span onClick={() => handleCounter('decrement')}>-</span>
                        <span>{count}</span>
                        <span onClick={() => handleCounter('increment')}>+</span>
                    </div>
                </div>
                <div className='product_amount'>
                    {product.map(({ amount, id }) => (
                        <span
                            key={amount}
                            className={productAmount === id ? 'active' : ''}
                            onClick={() => handleAmount(id)}>
                            {amount}
                        </span>
                    ))}
                </div>
                <span className='product_description'>{product[0].description}</span>

                <span className='adittions_title'>Добавление продуктов</span>

                <div className='additions_cards'>
                    {product[0].Additions?.map((addition) => (
                        <div
                            key={addition.name}
                            onClick={() => handleAddition(addition.id)}
                            className={`additions_card ${additionCheck.includes(addition.id) ? 'active' : ''}`}>
                            {additionCheck.includes(addition.id) ? (
                                <span className='additions_check_delete'>-</span>
                            ) : (
                                <span className='additions_check_add'>+</span>
                            )}

                            <img src={addition_img} alt='' />
                            <span className='additions_cards_title'>{addition.name}</span>
                            <span>20 грн.</span>
                        </div>
                    ))}
                </div>

                <span className='product_price'>
                    <span>Сумма: </span> <span> {price}</span>
                    <span>грн.</span>
                </span>
                <button className='product_buttuon' onClick={handleAddToCart}>
                    Добавить в корзину
                </button>
            </div>
        </>
    );
};

export default WorkerMobileModalContetnProduct;
