import { FC, useEffect } from 'react';
import { getClient } from '../../../../../store/slices/userAdminSlice';
import UseTable from '../../../../../useHook/UseTable/UseTable';
import { useAppDispatch, useAppSelector } from '../../../../../useHook/useRedux';
import { userColumn } from '../columnUsers';
import { delUser } from '../../../../../store/slices/userAdminSlice';

const UserClient: FC = () => {
    const dispatch = useAppDispatch();
    const clientData = useAppSelector((state) => state.adminUser.client);
    useEffect(() => {
        dispatch(getClient());
    }, []);

    return (
        <>
            <span className='userMain_title'>Client</span>
            {clientData && <UseTable columns={userColumn} link={true} data={clientData} delFunc={delUser} />}
        </>
    );
};

export default UserClient;
